/**=====================
    Profile Page CSS
========================**/
.account-box {
    display: flex;
    align-items: center;
    gap: 10px;

    .profile-image {
        width: 80px;
        height: 80px;

        .profile {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 8px;
        }
    }

    .account-content {
        width: calc(100% - 80px - 10px);
        @include flex_space;

        h3 {
            font-size: 18px;
            font-weight: 500;
            text-transform: capitalize;
            color: rgba(var(--title-color), 1);
        }

        h5 {
            margin-top: 4px;
            font-weight: 400;
            font-size: 14px;
            color: rgba(var(--content-color), 1);
        }

        .edit {
            font-size: 14px;
            font-weight: 400;
            color: rgba(var(--error-color), 1);
        }
    }
}

.account-list {
    margin-top: 25px;

    li {
        width: 100%;
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }

        .setting-box {
            display: flex;
            align-items: center;
            gap: 10px;
            padding: 10px 20px;
            border: 1px solid rgba(var(--border-color), 1);
            border-radius: 100px;

            .setting-icon {
                .main-icon {
                    --Iconsax-Color: rgba(var(--title-color), 1);
                    --Iconsax-Size: 24px;

                    &.main-img {
                        width: 20px;
                    }
                }
            }

            .setting-content {
                width: calc(100% - 24px - 10px);
                @include flex_space;

                h5 {
                    line-height: 1;
                    font-weight: 400;
                    font-size: 15px;
                    color: rgba(var(--title-color), 1);
                }

                h6 {
                    margin-top: 5px;
                    font-weight: 400;
                    color: rgba(var(--content-color), 1);
                }

                .arrow-icon {
                    --Iconsax-Color: rgba(var(--title-color), 1);
                    --Iconsax-Size: 18px;
                }
            }
        }
    }
}

.profile-img {
    position: relative;
    margin-left: auto;
    margin-right: auto;
    width: 80px;
    height: 80px;

    .profile {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 8px;
    }

    .edit-icon {
        padding: 5px;
        --Iconsax-Color: rgba(var(--theme-color), 1);
        --Iconsax-Size: 18px;
        background-color: rgba(var(--light-bg), 1);
        border-radius: 6px;
        position: absolute;
        bottom: -5px;
        right: -5px;
        cursor: pointer;
    }

    #file {
        position: absolute;
        bottom: 0;
        right: 0;
        width: 26px;
        height: 26px;
        z-index: 2;
        opacity: 0;
        cursor: pointer;
    }
}

.option-listing {
    margin-top: 15px;

    li {
        width: 100%;

        &:first-child {
            .form-check {
                padding-top: 0;
            }
        }

        &:last-child {
            .form-check {
                padding-bottom: 0;
                border-bottom: none;
            }
        }

        .form-check {
            display: flex;
            align-items: center;
            padding-left: 0;
            margin-bottom: 0;
            padding-block: 15px;
            border-bottom: 1px solid rgba(var(--border-color), 1);

            [dir="rtl"] & {
                padding-left: unset;
                padding-right: 0;
            }

            .form-check-label {
                width: 100%;
                font-weight: 400;
                font-size: 16px;
                color: rgba(var(--title-color), 1);
                margin-right: auto;
                display: flex;
                align-items: center;
                gap: 10px;

                .flag {
                    border-radius: 4px;
                }

                .icon {
                    --Iconsax-Color: rgba(var(--title-color), 1);
                    --Iconsax-Size: 20px;
                }

                @media (max-width: 600px) {
                    font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (768 - 320)));
                }
            }

            input[type=radio] {
                display: block;
            }

            .form-check-input {
                width: 20px;
                height: 20px;
                float: unset;
                margin: 0;
                box-shadow: none;
                background-color: rgba(var(--white), 1);
                border: 1px solid rgba(var(--border-color), 1);
                filter: none;

                &:checked {
                    position: relative;
                    background-color: rgba(var(--white), 1);
                    border: 1px solid rgba(var(--theme-color), 1);

                    &::after {
                        content: "";
                        position: absolute;
                        left: 50%;
                        top: 50%;
                        transform: translate(-50%, -50%);
                        width: 10px;
                        height: 10px;
                        background-color: rgba(var(--theme-color), 1);
                        border-radius: 100%;
                    }

                    &:focus {
                        box-shadow: none;
                    }
                }
            }
        }
    }
}

.profile-setting-details {
    margin-top: 20px;
    border-top: 1px solid rgba(var(--border-color), 1);
}

.grocery-account-list {
    li {
        .setting-box {
            padding: 8px 12px;
            background-color: rgba(var(--light-bg), 1);
            border-radius: 10px;
            border: 1px solid rgba(var(--border-color), 1);

            .setting-icon {
                width: 40px;
                height: 40px;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 100%;
                background-color: rgba(var(--white), 1);
            }

            .account-content {
                .arrow-icon {
                    --Iconsax-Color: rgba(var(--content-color), 1);
                }
            }
        }
    }
}

.grocery-profile-background {
    border-radius: 6px;
    margin-top: 40px;
    background-color: rgba(var(--light-bg), 1);

    .profile-part {
        padding: 0 20px 20px;
        text-align: center;

        .profile-pic {
            width: 80px;
            height: 80px;
            object-fit: cover;
            border-radius: 10px;
            margin-top: -40px;
        }

        h3 {
            margin-top: 14px;
            color: rgba(var(--title-color), 1);
        }

        h4 {
            font-size: 13px;
            font-weight: 500;
            color: rgba(var(--content-color), 1);
        }
    }
}



.food-profile-background {
    border-radius: 6px;
    margin-top: 50px;
    background-color: rgba(var(--light-bg), 1);

    .profile-part {
        padding: 0 20px 20px;
        text-align: center;

        .profile-pic {
            width: 80px;
            height: 80px;
            object-fit: cover;
            border-radius: 100%;
            margin-top: -40px;
        }

        h3 {
            margin-top: 14px;
            color: rgba(var(--title-color), 1);
        }

        h4 {
            font-size: 13px;
            font-weight: 500;
            color: rgba(var(--content-color), 1);
        }
    }
}


.food-account-list {
    margin-top: 16px;

    ~.food-account-list {
        margin-top: 22px;
    }

    .profile-listing {
        margin: 0;

        +.profile-listing {
            border-top: 1px solid rgba(var(--bg-color), 1);
            margin-top: 10px;
            padding-top: 10px;
        }

        .setting-box {
            padding: 0;
            border: none;

            .setting-icon {
                width: 40px;
                height: 40px;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 100%;
                background-color: rgba(var(--bg-color), 1);

                .main-icon {
                    --Iconsax-Size: 20px;
                }
            }

            .account-content {
                .arrow-icon {
                    --Iconsax-Color: rgba(var(--content-color), 1);
                }
            }

            .toggle-left-to-right {
                display: flex;
                justify-content: center;
                align-items: center;

                .toggle-button {
                    display: flex;
                    position: relative;

                    span {
                        display: flex;
                        position: relative;
                        width: 60px;
                        height: 32px;
                        border-radius: 100px;
                        background-color: rgba(var(--bg-color), 1);
                        transition: 0.5s;
                        cursor: pointer;

                        &::before {
                            @include pos;
                            width: 20px;
                            height: 20px;
                            top: 6px;
                            left: 5px;
                            border-radius: 100%;
                            background-color: rgba(var(--content-color), 0.5);
                            transition: 0.5s;
                        }
                    }

                    input[type="checkbox"] {
                        opacity: 0;
                        width: 0;
                        height: 0;

                        &:checked+span {
                            background-color: rgba(var(--theme-color), 0.3);

                            &::before {
                                left: 35px;
                                background-color: rgba(var(--theme-color), 1);
                            }
                        }
                    }
                }
            }
        }
    }
}

.setting-title {
    margin: 0;

    ~.setting-title {
        margin-top: 30px;
    }
}

/* ==================
chatting profile
=====================*/
.chatting-profile-img {
    @include pseudowh(106px, 106px);
    position: absolute;
    top: -35px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    border: 5px solid rgba(var(--white), 1);
    border-radius: 100%;

    @media (max-width:600px) {
        @include pseudowh(calc(90px + (106 - 90) * ((90vw - 320px) / (600 - 320))), calc(90px + (106 - 90) * ((100vw - 320px) / (600 - 320))));
    }

    .edit-icon {
        padding: 5px;
        --Iconsax-Color: rgba(var(--light-blue), 1);
        --Iconsax-Size: 18px;
        background-color: rgba(var(--light-bg), 1);
        position: absolute;
        bottom: 6px;
        right: -6px;
        cursor: pointer;

        @media (max-width:600px) {
            --Iconsax-Size: calc(16px + (18 - 16) * ((90vw - 320px) / (600 - 320)))
        }
    }

    #file {
        position: absolute;
        bottom: 0;
        right: 0;
        z-index: 2;
        opacity: 0;
        cursor: pointer;
    }
}

.profile-body {
    margin-top: 60px;

    @media (max-width:600px) {
        margin-top: calc(48px + (60 - 48) * ((100vw - 320px) / (600 - 320)));
    }

    .profile-setting {
        margin-top: 16px;

        h3 {
            font-size: 16px;
            font-weight: 700;
            color: rgba(var(--title-color), 1);
        }

        .profile-setting-content {
            margin-top: 15px;

            li {
                font-size: 16px;
                font-weight: 500;
                color: rgba(var(--title-color), 1);
                display: flex;
                align-items: center;
                gap: 5px;
                cursor: pointer;

                +li {
                    margin-top: 18px;
                }

                a {
                    display: flex;
                    align-items: center;
                    gap: 5px;
                }

                i {
                    --Iconsax-Color: rgba(var(--title-color), 1);
                    --Iconsax-Size: 20px;
                }

                .mute {
                    cursor: pointer;
                    display: inline-flex;
                    align-items: center;
                    gap: 5px;
                    padding: 0;


                    .icon {
                        display: inline;
                    }

                    .mute-icon {
                        display: none;
                    }

                    input.toggle-mute:checked~.icon {
                        display: none;
                    }

                    input.toggle-mute:checked~.mute-icon {
                        display: inline;
                    }
                }
            }
        }
    }
}



/* =============
modal 
================*/
.modal-content {
    padding: 20px;
    border: none;

    p {
        font-size: 18px;
        font-weight: 600;
        color: rgba(var(--title-color), 1);
    }
}


/* ===================
Hotel profile CSS 
======================*/
.hotel-profile-wrapper {
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    position: relative;

    img {
        @include pseudowh (100px, 100px);
        border-radius: 100%;
        padding: 7px;
        background-color: rgba(var(--white), 1);

    }

    h3 {
        font-size: 18px;
        font-weight: 600;
        color: rgba(var(--title-color), 1);
    }

    h4 {
        margin-top: 3px;
        font-size: 15px;
        font-weight: 400;
        color: rgba(var(--content-color), 1);
    }
}

.hotel-profile-section {
    margin-top: 28px;

    li {
        width: 100%;

        +li {
            border-top: 1px dashed rgba(var(--content-color), 0.5);
            padding-top: 18px;
            margin-top: 15px;

            @media (max-width: 600px) {
                padding-top: calc(12px + (18 - 12) * ((100vw - 320px) / (600 - 320)));
                margin-top: calc(12px + (15 - 12) * ((100vw - 320px) / (600 - 320)));
            }
        }

        a {
            display: flex;
            align-items: center;
            gap: 12px;

            .icon {
                --Iconsax-Size: 20px;
                --Iconsax-Color: rgba(var(--title-color), 1);
            }

            .hotel-profile-content {
                width: calc(100% - 20px - 12px);
                display: flex;
                align-items: center;
                justify-content: space-between;

                h5 {
                    font-size: 16px;
                    font-weight: 500;
                    color: rgba(var(--title-color), 1);
                }

                .toggle-left-to-right {
                    @include flex_center;

                    .toggle-button {
                        appearance: unset;
                        display: flex;
                        position: relative;
                        width: 60px;
                        height: 32px;
                        border-radius: 100px;
                        background-color: rgba(var(--content-color), 0.1);
                        transition: 0.5s;
                        cursor: pointer;

                        @media (max-width: 600px) {
                            width: calc(49px + (60 - 49) * ((100vw - 320px) / (600 - 320)));
                            height: calc(24px + (32 - 24) * ((100vw - 320px) / (600 - 320)));
                        }

                        &::before {
                            @include pos;
                            width: 20px;
                            height: 20px;
                            top: 6px;
                            left: 5px;
                            border-radius: 100%;
                            background-color: rgba(var(--content-color), 0.5);
                            transition: 0.5s;

                            @media (max-width: 600px) {
                                width: calc(16px + (20 - 16) * ((100vw - 320px) / (600 - 320)));
                                height: calc(16px + (20 - 16) * ((100vw - 320px) / (600 - 320)));
                                top: calc(4px + (6 - 4) * ((100vw - 320px) / (600 - 320)));
                            }
                        }

                        &:checked {
                            background-color: rgba(var(--theme-color), 0.3);

                            &::before {
                                left: 35px;
                                background-color: rgba(var(--theme-color), 1);

                                @media (max-width: 600px) {
                                    left: calc(27px + (35 - 27) * ((100vw - 320px) / (600 - 320)));
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .hotel-last-content {
        .icon {
            --Iconsax-Color: rgba(var(--theme-color), 1);
        }

        .hotel-profile-content {
            h5 {
                color: rgba(var(--theme-color), 1);
            }

        }

    }
}


/*=====================
Blog profile page CSS 
=======================*/
.blog-profile-list {
    @include flex_center;
    flex-direction: column;


    img {
        @include pseudowh(78px, 78px);
        border-radius: 100%;

        @media (max-width:600px) {
            @include pseudowh(calc(65px + (78 - 65) * ((100vw - 320px) / (600 - 320))), calc(65px + (78 - 65) * ((100vw - 320px) / (600 - 320))));
        }
    }

    .blog-profile-content {
        margin-top: 8px;
        text-align: center;

        h4 {
            font-size: 16px;
            font-weight: 500;
            color: rgba(var(--title-color), 1);
        }

        a {
            font-size: 14px;
            font-weight: 300;
            color: rgba(var(--content-color), 1);

        }
    }
}