/* Fonts */
$metropolis: "Metropolis",
sans-serif;
$lato: "Lato",
serif;
$anton: "Anton",
serif;
$nunito: "Nunito Sans",
serif;
$mulish: "Mulish",
sans-serif;
$lato:"Lato",
sans-serif;
$noto: "Noto Sans",
sans-serif;
$roboto:"Roboto",
sans-serif;
$manrope:"Manrope",
sans-serif;
$mukta: 'Mukta';
$outfit: "Outfit",
sans-serif;

:root {
    --theme-color: 255, 199, 47;
    --title-color: 47, 48, 50;
    --content-color: 157, 172, 177;
    --white: 255, 255, 255;
    --black: 0, 0, 0;
    --light-bg: 245, 245, 245;
    --dark-bg: 32, 32, 32;
    --border-color: 221, 221, 221;
    --success-color: 25, 135, 84;
    --error-color: 255, 76, 59;
    --secondary-color: 255, 199, 47;
    --rating-color: 255, 199, 47; // #ffc72f
    --gradient: linear-gradient(90deg, #ffc72f 0%, rgba(32, 32, 32, 0.11) 100%);
}

.ecommerce {
    --theme-color: 82, 104, 102;
    --title-color: 34, 34, 34;
    --content-color: 141, 143, 141;
    --light-bg: 245, 245, 245;
    --dark-bg: 41, 51, 50;
    --border-color: 244, 245, 248;
    --form-border: 221, 221, 221;
}

.plant {
    --theme-color: 13, 151, 105;
    --title-color: 49, 49, 49;
    --content-color: 141, 143, 141;
    --light-bg: 243, 243, 243;
    --border-color: 241, 241, 241;
}

.grocery {
    --theme-color: 65, 167, 167;
    --title-color: 49, 49, 49;
    --content-color: 141, 143, 141;
    --light-bg: 243, 243, 243;
    --border-color: 241, 241, 241;
}

.pet {
    --theme-color: 6, 75, 181; //#064BB5
    --title-color: 49, 49, 49;
    --content-color: 141, 143, 141;
    --light-bg: 245, 245, 245;
    --border-color: 218, 218, 218;
}

.food {
    --theme-color: 255, 199, 47; // #FFC72F
    --content-color: 141, 143, 141; // #8D8F8D
    --bg-color: 245, 245, 245; // #F5F5F5
    --blue: 0, 144, 249; //#0090F9
    --light-green: 23, 156, 27; //#179C1B
    --green: 8, 152, 83; //#089853
    --border: 214, 214, 214; //#D6D6D6
}

.chatting {
    --theme-color: linear-gradient(180deg, rgba(95, 149, 255, 1) 0%, rgba(34, 109, 255, 1) 50%);
    --secondary: 14, 31, 64; //#0E1F40
    --light-blue: 34, 109, 255; //#226DFF
    --title-color: 49, 49, 49; //#313131
    --content-color: 141, 143, 141; //#8D8F8D
    --light-color: 163, 193, 250; // 
    --online: 77, 214, 140; //#4DD68C
}

.hotel-booking {
    --theme-color: 0, 122, 196; // #007AC4
    --title-color: 49, 49, 49; //#313131
    --content-color: 141, 143, 141; //#8D8F8D
    --light-gray: 242, 242, 242; // #F2F2F2

    &.dark-mode {
        --light-gray: 224, 224, 224; // #F2F2F2
        --white: 19, 20, 22;
    }
}

.blog {
    --theme-color: 165, 99, 77; //#A5634D
    --title-color: 49, 49, 49; // #313131
    --content-color: 141, 143, 141; //#8D8F8D
}

.learning {
    --theme-color: 255, 155, 92; //#FF9B5C
    --secondary-color: 153, 187, 78; // #99BB4E
    --light-blue: 175, 209, 223; //#AFD1DF
    --title-color: 49, 49, 49; //#313131
    --content-color: 141, 143, 141; //#8D8F8D
}