/**=====================
     Header CSS
========================**/
.header {
     display: block;
     padding: 20px 0 10px;
     background-color: rgba(var(--white), 1);
     z-index: 2;
     position: fixed;
     top: 0;
     left: 50%;
     transform: translateX(-50%);
     max-width: 600px;
     width: 100%;

     .head-content {
          display: flex;
          align-items: center;
          gap: 8px;
          width: 100%;

          .sidebar-btn {
               width: 40px;
               height: 40px;
               --Iconsax-Color: rgba(var(--title-color), 1);
               background-color: rgba(var(--light-bg), 1);
               padding: 10px;
               border-radius: 100%;
               display: flex;
               justify-content: center;
               align-items: center;
               border: none;

               @media (max-width: 600px) {
                    width: calc(35px + (40 - 35) * ((100vw - 320px) / (600 - 320)));
                    height: calc(35px + (40 - 35) * ((100vw - 320px) / (600 - 320)));
               }

               i {
                    --Iconsax-Size: 20px;

                    @media (max-width: 600px) {
                         --Iconsax-Size: calc(18px + (20 - 18) * ((100vw - 320px) / (600 - 320)));
                    }
               }
          }

          .header-location {
               display: flex;
               align-items: center;
               justify-content: unset;
               gap: 8px;
               width: 100%;
               background: none;
               padding: 0;

               .location-icon {
                    width: 40px;
                    height: 40px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background-color: rgba(var(--theme-color), 0.1);
                    border-radius: 100%;

                    @media (max-width: 600px) {
                         width: calc(32px + (40 - 32) * ((100vw - 320px) / (600 - 320)));
                         height: calc(32px + (40 - 32) * ((100vw - 320px) / (600 - 320)));
                    }

                    .location {
                         width: 20px;
                         height: 20px;
                         object-fit: cover;

                         @media (max-width: 600px) {
                              width: calc(18px + (20 - 18) * ((100vw - 320px) / (600 - 320)));
                              height: calc(18px + (20 - 18) * ((100vw - 320px) / (600 - 320)));
                         }
                    }
               }

               .profile-icon {
                    width: 40px;
                    height: 40px;

                    @media (max-width: 600px) {
                         width: calc(32px + (40 - 32) * ((100vw - 320px) / (600 - 320)));
                         height: calc(32px + (40 - 32) * ((100vw - 320px) / (600 - 320)));
                    }
               }

               .location-content {
                    width: calc(100% - calc(32px + (40 - 32) * ((100vw - 320px) / (600 - 320))) - 5px);

                    h6 {
                         font-weight: 600;
                         color: rgba(var(--title-color), 1);
                    }

                    h5 {
                         color: rgba(var(--content-color), 1);
                         overflow: hidden;
                         text-overflow: ellipsis;
                         display: -webkit-box;
                         -webkit-line-clamp: 1;
                         -webkit-box-orient: vertical;
                    }

                    .d-arrow {
                         --Iconsax-Color: rgba(var(--title-color), 1);
                         --Iconsax-Size: 14px;
                    }
               }
          }
     }

     a {
          width: 40px;
          height: 40px;
          background-color: rgba(var(--light-bg), 1);
          border-radius: 100%;
          display: flex;
          justify-content: center;
          align-items: center;

          @media (max-width: 600px) {
               width: calc(35px + (40 - 35) * ((100vw - 320px) / (600 - 320)));
               height: calc(35px + (40 - 35) * ((100vw - 320px) / (600 - 320)));
          }

          .icon-btn {
               --Iconsax-Color: rgba(var(--title-color), 1);
               --Iconsax-Size: 20px;

               @media (max-width: 600px) {
                    --Iconsax-Size: calc(18px + (20 - 18) * ((100vw - 320px) / (600 - 320)));
               }

               &.notification-icon {
                    position: relative;

                    &::after {
                         content: "";
                         position: absolute;
                         top: 2px;
                         right: 2px;
                         width: 5px;
                         height: 5px;
                         border-radius: 100%;
                         background-color: rgba(var(--error-color), 1);

                    }
               }
          }

     }

     &.plant-header {
          .profile-content {
               h4 {
                    @include flex_common;
                    gap: 5px;
                    font-size: 16px;
                    font-weight: 700;
                    color: rgba(var(--title-color), 1);

                    .hand {
                         width: 20px;
                    }
               }

               h6 {
                    font-size: 12px;
                    font-weight: 400;
                    color: rgba(var(--content-color), 1);
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;
               }
          }
     }

     &.grocery-header {
          .head-content {
               .profile-content {
                    .logo {
                         width: 100px;
                    }
               }

               .points {
                    font-size: 14px;
                    font-weight: 600;
                    color: rgba(var(--title-color), 1);
               }
          }

          a {
               background-color: rgba(var(--theme-color), 0.1);

               .notification-icon {
                    --Iconsax-Color: rgba(var(--theme-color), 1);
               }
          }
     }
}

.main-header {
     background-color: rgba(var(--white), 1);
     z-index: 2;
     position: fixed;
     top: 0;
     left: 50%;
     transform: translateX(-50%);
     max-width: 600px;
     width: 100%;
     padding-top: 20px;

     .header-panel {
          position: relative;
          padding-bottom: 10px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          line-height: 1;
          z-index: 1;

          .sidebar-btn {
               width: 40px;
               height: 40px;
               --Iconsax-Color: rgba(var(--title-color), 1);
               background-color: rgba(var(--light-bg), 1);
               padding: 10px;
               border-radius: 100%;
               display: flex;
               justify-content: center;
               align-items: center;
               border: none;

               @media (max-width: 600px) {
                    width: calc(35px + (40 - 35) * ((100vw - 320px) / (600 - 320)));
                    height: calc(35px + (40 - 35) * ((100vw - 320px) / (600 - 320)));
               }

               i {
                    --Iconsax-Size: 20px;

                    @media (max-width: 600px) {
                         --Iconsax-Size: calc(16px + (20 - 16) * ((100vw - 320px) / (600 - 320)));
                    }
               }
          }

          a {
               position: relative;
               display: flex;
               align-items: center;
               justify-content: center;
               z-index: 1;

               @media (max-width: 600px) {
                    --Iconsax-Size: calc(16px + (20 - 16) * ((100vw - 320px) / (600 - 320)));
               }

               .icon-btn {
                    position: relative;
                    --Iconsax-Color: rgba(var(--title-color), 1);
                    --Iconsax-Size: 20px;
                    line-height: 1;

                    @media (max-width: 600px) {
                         --Iconsax-Size: calc(18px + (20 - 18) * ((100vw - 320px) / (600 - 320)));
                    }

                    &.trash-icon {
                         background-color: rgba(var(--error-color), 0.1);
                         --Iconsax-Color: rgba(var(--error-color), 1);
                    }

                    &.menu {
                         transform: rotate(90deg);
                    }

                    &.like-btn {
                         background-color: rgba(var(--error-color), 0.1);
                         border-radius: 100%;
                    }

                    &.notification-icon {
                         position: relative;

                         &::after {
                              content: "";
                              position: absolute;
                              top: 2px;
                              right: 2px;
                              width: 5px;
                              height: 5px;
                              border-radius: 100%;
                              background-color: rgba(var(--error-color), 1);

                              [dir="rtl"] & {
                                   right: unset;
                                   left: 1px;
                              }
                         }
                    }
               }

               span {
                    font-size: 14px;
                    font-weight: 400;
                    color: rgba(var(--content-color), 1);
               }
          }


          .icons {
               --Iconsax-Color: rgba(var(--title-color), 1);
               --Iconsax-Size: 16px;
               background-color: rgba(var(--white), 0.1);
               box-shadow: 0px 2px 8px rgba(var(--title-color), 0.1);
               padding: 10px;
               border: 1px solid rgba(var(--title-color), 0.1);
               border-radius: 100%;
               display: flex;
               justify-content: center;
               align-items: center;
               width: 40px;
               height: 40px;

               @media (max-width: 600px) {
                    width: calc(35px + (40 - 35) * ((100vw - 320px) / (600 - 320)));
                    height: calc(35px + (40 - 35) * ((100vw - 320px) / (600 - 320)));
               }
          }

          h3 {
               padding-inline: 40px;
               overflow: hidden;
               white-space: nowrap;
               text-overflow: ellipsis;
               width: 100%;
               position: absolute;
               font-weight: 600;
               left: 50%;
               top: 50%;
               transform: translate(-50%, -50%);
               text-align: center;
               font-size: 18px;
               padding-bottom: 10px;
               color: rgba(var(--title-color), 1);
          }
     }
}

