header {
     .food-head {
          display: flex;
          align-items: center;
          gap: 8px;
          width: 100%;

          .header-location {
               @include flex_center(flex, center, unset);
               gap: 12px;
               width: 100%;
               background: none;
               padding: 0;

               .head-location-icon {
                    @include flex_center;
                    @include pseudowh (40px, 40px);
                    background-color: rgba(var(--theme-color), 0.15);
                    border-radius: 6px;

                    svg {
                         @include pseudowh(24px, 24px);
                         fill: rgba(var(--theme-color), 1);
                    }

                    @media (max-width:600px) {
                         width: calc(35px + (40 - 35) * ((100vw - 320px) / (600 - 320)));
                         height: calc(35px + (40 - 35) * ((100vw - 320px) / (600 - 320)));
                    }

               }

               .head-location-contant {
                    width: calc(100% - 40px - 12px);


                    h6 {
                         font-size: 16px;
                         font-weight: 600;
                         color: rgba(var(--dark-bg), 1);
                    }

                    .down-arrow {
                         --Iconsax-Color: rgba(var(--dark-bg), 1);
                         --Iconsax-Size: 22px;

                         @media (max-width:600px) {
                              --Iconsax-Size: calc(20px + (22 - 20) * ((100vw - 320px) / (600 - 320)));

                         }
                    }

                    h5 {
                         @include text-ellipsis;
                         font-weight: 400;
                         line-height: 23px;
                         color: rgba(var(--content-color), 1);

                         @media (max-width:600px) {
                              font-size: calc(13px + (14 - 13) * ((100vw - 320px) / (600 - 320)));

                         }
                    }
               }
          }
     }

     a {
          position: relative;

          span {
               @include flex_center;
               @include pseudowh(12px, 12px);
               position: absolute;
               top: 5px;
               left: 20px;
               background-color: rgba(var(--theme-color), 1);
               border-radius: 50px;
               color: rgba(var(--dark-bg), 1);
               font-size: 10px;
               font-weight: 700;
               padding: 5px;
          }

          .left {
               --Iconsax-Color: rgba(var(--dark-bg), 1);
               --Iconsax-Size: 22px;
          }

     }
}


