/******** chatting header CSS *******/
.chatting-main-header {
     width: 100%;
     max-width: 600px;
     position: relative;
     padding-block: 34px 58px;
     background: var(--theme-color);

     @media (max-width:600px) {
          padding-top: calc(26px + (34 - 26) * ((100vw - 320px) / (600 - 320)));
     }

     // &::before {
     //      @include pos;
     //      width: 100%;
     //      height: 30px;
     //      bottom: 0;
     //      max-width: 600px;
     //      background-color: rgba(var(--white), 1);
     //      border-radius: 30px 30px 0 0;
     // }

     .icon-btn {
          --Iconsax-Size: 24px;
          --Iconsax-Color: rgba(var(--white), 1);
     }

     .header-box {
          display: flex;
          align-items: center;

          h2 {
               font-size: 22px;
               font-weight: 800;
               color: rgba(var(--white), 1);

               @media (max-width:600px) {
                    font-size: calc(20px + (22 - 20) * ((100vw - 320px) / (600 - 320)));
               }
          }

          .header-profile {
               display: flex;
               align-items: center;
               gap: 10px;
               margin-left: 10px;

               img {
                    @include pseudowh(40px, 40px);
               }

               .profile-content {
                    color: rgba(var(--white), 1);
                    font-size: 14px;

                    h5 {
                         font-weight: 600;
                    }

                    h6 {
                         margin-top: 3px;
                         font-weight: 400;
                    }

               }
          }

          .right-header {
               margin-left: auto;
               margin-left: auto;
               display: flex;
               align-items: baseline;
               gap: 12px;

               @media (max-width:600px) {
                    gap: calc(8px + (12 - 8) * ((100vw - 320px) / (600 - 320)));
               }

               a {
                    .icon {
                         --Iconsax-Size: 24px;
                         --Iconsax-Color: rgba(var(--white), 1);

                         @media (max-width:600px) {
                              --Iconsax-Size: calc(22px + (24 - 22) * ((100vw - 320px) / (600 - 320)));
                         }
                    }
               }

               .chatting-dropdown {
                    .dropdown-menu {
                         width: 100%;
                         max-width: 168px;
                         background-color: rgba(var(--white), 1);
                         border: 1px solid rgba(var(--content-color), 0.1);
                         box-shadow: 0 2px 15px 0 rgba(44, 44, 44, 0.08);
                         border-radius: 8px;
                         padding: 14px 12px;
                         margin-top: 10px !important;

                         li {
                              width: 100%;

                              +li {
                                   margin-top: 5px;
                                   padding-top: 10px;
                                   border-top: 1px solid rgba(var(--content-color), 0.2);
                              }

                              .dropdown-item {
                                   font-size: 14px;
                                   font-weight: 500;
                                   color: rgba(var(--title-color), 0.8);
                                   padding: 0;
                                   display: flex;
                                   align-items: center;
                                   gap: 8px;

                                   .menu-icon {
                                        --Iconsax-Color: rgba(var(--title-color), 0.8);
                                        --Iconsax-Size: 16px;
                                   }

                                   &:hover {
                                        background-color: transparent;
                                   }
                              }
                         }

                    }

               }
          }

          .search-box {
               position: absolute;
               left: 0;
               top: 30px;
               width: 100%;
               opacity: 0;
               visibility: hidden;
               transform: scale(0);
               transition: all 0.5s ease;
               z-index: 1;
               padding: 0 20px;

               input {
                    border: none;
                    padding: 10px 34px;
               }

               .add-icon {
                    --Iconsax-Color: rgba(var(--content-color), 1);
                    --Iconsax-Size: 24px;
                    rotate: 43deg;
                    position: absolute;
                    top: 12px;
                    right: 24px;
                    cursor: pointer;
               }

               .icon {
                    --Iconsax-Color: rgba(var(--content-color), 1);
                    --Iconsax-Size: 22px;
                    position: absolute;
                    top: 12px;
                    left: 24px;
                    cursor: pointer;

               }

               &.active {
                    opacity: 1;
                    visibility: visible;
                    transform: scale(1);
               }
          }
     }

     .header-panel {
          position: relative;
          display: flex;
          justify-content: space-between;
          align-items: center;
          line-height: 1;
          z-index: 1;

          h3 {
               overflow: hidden;
               white-space: nowrap;
               text-overflow: ellipsis;
               display: inline-flex;
               line-height: 1.4;
               position: absolute;
               font-weight: 700;
               left: 50%;
               top: 50%;
               transform: translate(-50%, -50%);
               text-align: center;
               font-size: 22px;
               color: rgba(var(--white), 1);

               @media (max-width:600px) {
                    font-size: calc(20px + (22 - 20) * ((100vw - 320px) / (600 - 320)));
               }
          }
     }

     &-lg {
          padding-bottom: 74px;
     }
}

.audio-header {
     background: transparent;
     position: absolute;
     top: 0;
     padding-top: 35px;

     &::before {
          content: none;
     }

     .header-panel {
          a {
               .icon {
                    background-color: rgba(var(--white), 1);
                    padding: 5px;
                    border-radius: 100%;
                    --Iconsax-Color: rgba(var(--title-color), 1);
                    --Iconsax-Size: 24px;
               }

               .add {
                    rotate: 45deg;
               }
          }
     }

     @media (max-width:600px) {
          padding-top: calc(40px + (57 - 40) * ((100vw - 320px) / (600 - 320)));

     }
}



