     padding-top: 58px;

     @media (max-width:600px) {
          padding-top: calc(20px + (58 - 20) * ((100vw - 320px) / (600 - 320)));

     }

     .header-panel {
          position: relative;
          display: flex;
          justify-content: space-between;
          align-items: center;
          line-height: 1;
          z-index: 1;

          a {
               position: relative;
               z-index: 1;

               @media (max-width: 600px) {
                    --Iconsax-Size: calc(16px + (20 - 16) * ((100vw - 320px) / (600 - 320)));
               }

               .back-icon {
                    --Iconsax-Color: rgba(var(--title-color), 1);
                    --Iconsax-size: 24px;
               }
          }
     }
}

