/* ===================
Blog header CSS 
=======================*/
.blog-header {
     background-color: rgba(var(--white), 1);
     padding: 45px 10px 0 0;
     position: fixed;
     z-index: 1;
     max-width: 600px;
     width: 100%;

     @media (max-width:600px) {
          padding-top: calc(30px + (45 - 30) * ((100vw - 320px) / (600 - 320)));
     }

     .header-panel {
          h3 {
               font-size: 26px;
               font-weight: 600;

               @media (max-width:600px) {
                    font-size: calc(22px + (26 - 22) * ((100vw - 320px) / (600 - 320)));
               }
          }

          .icon-btn {
               --Iconsax-Size: 26px;

               @media (max-width:600px) {
                    --Iconsax-Size: calc(24px + (26 - 24) * ((100vw - 320px) / (600 - 320)));
               }
          }


          .skip {
               font-size: 16px;
               font-weight: 500;
               color: rgba(var(--content-color), 1);
          }
     }
}


.blog-main-header {
     position: fixed;
     padding-bottom: 10px;
     width: 100%;
     max-width: 600px;
     background-color: rgba(var(--white), 1);
     z-index: 3;

     .header-content {
          display: flex;
          align-items: center;
          gap: 15px;

          .icon {
               --Iconsax-Size: 26px;
               --Iconsax-Color: rgba(var(--title-color), 1);

               @media (max-width:600px) {
                    --Iconsax-Size: calc(24px + (26 - 24) * ((100vw - 320px) / (600 - 320)));
               }
          }

          .icon-btn {
               --Iconsax-Color: rgba(var(--title-color), 1);
               --Iconsax-Size: 24px;

               @media (max-width:600px) {
                    --Iconsax-Size: calc(20px + (24 - 20) * ((100vw - 320px) / (600 - 320)));
               }
          }

          .blog-main-content {
               display: flex;
               align-items: center;
               justify-content: space-between;
               width: calc(100% - 24px - 15px);

               h4 {
                    font-size: 18px;
                    font-weight: 700;
                    color: rgba(var(--title-color), 1);
               }

               h2 {
                    font-size: 26px;
                    font-weight: 600;

                    @media (max-width:600px) {
                         font-size: calc(22px + (26 - 22) * ((100vw - 320px) / (600 - 320)));
                    }
               }

               h3 {
                    font-size: 22px;
                    font-weight: 500;
                    color: rgba(var(--title-color), 1);

                    @media (max-width:600px) {
                         font-size: calc(20px + (22 - 20) * ((100vw - 320px) / (600 - 320)));

                    }
               }

               img {
                    cursor: pointer;
                    @include pseudowh (34px, 34px);
                    border-radius: 100%;
               }

               .all {
                    font-size: 14px;
                    font-weight: 500;
                    color: rgba(var(--theme-color), 1);
               }

               .skip {
                    font-size: 16px;
                    font-weight: 500;
                    color: rgba(var(--content-color), 1);
               }

          }
     }
}

.post {
     padding-top: 18px;
     padding-right: 0;

     h2 {
          font-size: 18px;
          font-weight: 500;
          color: rgba(var(--title-color), 1);
     }

     a {
          .icon-btn {
               rotate: 45deg;
          }
     }
}

.forward-header {
     display: flex;
     justify-content: space-between;
     position: absolute;
     top: 20px;
     z-index: 1;
     width: -webkit-fill-available;
     padding-inline: 20px;

     .icon {
          @include flex_center;
          @include pseudowh(30px, 30px);
          background-color: rgba(var(--white), 1);
          border-radius: 100%;
          border: none;

          .icon-btn {
               --Iconsax-Color: rgba(var(--title-color), 1);
               --Iconsax-Size: 24px;
          }
     }


     img {
          @include pseudowh(18px, 18px);
     }

     .message-icon {
          display: flex;
          gap: 8px;
     }

}

.blog-search-header {
     display: flex;
     align-items: center;
     gap: 15px;
     padding: 19px 15px 0;
     position: relative;
     inset: unset;
     width: 100%;

     .icon-btn {
          --Iconsax-Color: rgba(var(--title-color), 1);
          --Iconsax-Size: 24px;
     }

     .blog-search-input {
          position: relative;
          width: 100%;

          .icon {
               --Iconsax-Color: rgba(var(--content-color), 1);
               --Iconsax-Size: 20px;
          }

          input {
               font-size: 14px;
               font-weight: 400;
               background-color: rgba(var(--white), 1);
               color: rgba(var(--content-color), 1);
               border: none;
               border-radius: 5px;
               padding: 15px 40px;
               box-shadow: 0 -1px 17px 0 rgba(var(--black), 0.05);
          }

          .search {
               position: absolute;
               left: 10px;
               top: 50%;
               transform: translateY(-50%);
               font-size: 20px;
               color: rgba(var(--content-color), 1);
          }

          .mic {
               position: absolute;
               right: 10px;
               top: 50%;
               transform: translateY(-50%);
               font-size: 20px;
               color: rgba(var(--content-color), 1);
          }
     }
}

