/**=====================
     Product box CSS
========================**/
.product-box {
    height: 100%;
    background-color: rgba(var(--white), 1);

    .img-part {
        box-shadow: 0px 2px 4px rgba(var(--black), 0.05);
        border-radius: 3px;
        position: relative;
        overflow: hidden;

        .product-image {
            width: 100%;

            .product {
                width: 100%;
            }
        }

        .wishlist-btn {
            padding: 4px !important;
            border-radius: 100%;
            padding: 0;
            position: absolute;
            top: 10px;
            right: 10px;
            box-shadow: 0px 2px 4px rgba(var(--black), 0.04);
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: rgba(var(--white), 1) !important;

            &.active,
            :active,
            :hover {
                border-color: transparent !important;
            }

            &:not(.btn-check)+.btn:active {
                border-color: transparent !important;
            }

            .icli {
                color: rgba(var(--content-color), 1);
            }

            .icbo {
                display: none;
            }

            &.active {
                .icli {
                    display: none;
                }

                .icbo {
                    display: flex;
                    color: rgba(var(--theme-color), 1);
                }

                .effect:before {
                    animation: fireworkLine 0.5s linear 0.1s;
                }

                .effect:after {
                    animation: fireworkPoint 0.5s linear 0.1s;
                }
            }
        }
    }

    .product-content {
        margin-top: 8px;

        .ratings {
            display: inline-flex;
            align-items: center;
            gap: 5px;
            padding: 4px 12px;
            line-height: 1;
            font-size: 12px;
            font-weight: 400;
            background-color: rgba(var(--secondary-color), 0.1);
            border-radius: 100px;

        }

        .product-name {
            margin-top: 4px;
            color: rgba(var(--title-color), 1);
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis;
            font-weight: 500;
            font-size: 14px;
        }

        .price {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            gap: 4px;
            margin-top: 4px;
            font-size: 13px;
            font-weight: 400;
            color: rgba(var(--title-color), 1);

            del {
                color: rgba(var(--content-color), 1);
            }

            span {
                color: rgba(var(--error-color), 1);
            }
        }
    }
}

.horizontal-product-box {
    background-color: rgba(var(--white), 1);
    border: 1px solid rgba(var(--border-color), 1);
    border-radius: 3px;

    .product-image {
        width: 100%;

        .product {
            width: 100%;
        }
    }

    .product-content {
        padding: 10px;

        .product-name {
            font-size: 14px;
            font-weight: 600;
            color: rgba(var(--title-color), 1);
            font-weight: 500;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        .price {
            display: flex;
            align-items: center;
            gap: 4px;
            margin-top: 4px;
            font-size: 13px;
            font-weight: 400;
            color: rgba(var(--title-color), 1);

            del {
                color: rgba(var(--content-color), 1);
            }

            span {
                color: rgba(var(--error-color), 1);
            }
        }

        .tag-list {
            margin-top: 10px;
            display: flex;
            align-items: center;
            gap: 10px;

            li {
                font-size: 12px;
                font-weight: 500;
                padding: 5px 15px;
                color: rgba(var(--content-color), 1);
                background-color: rgba(var(--light-bg), 1);
                border-radius: 5px;
            }
        }
    }
}

.cart-product-box {
    width: 100%;
    padding: 10px;
    background-color: rgba(var(--light-bg), 1);
    border: 1px solid rgba(var(--border-color), 1);
    border-radius: 6px;
    display: flex;
    align-items: center;
    gap: 8px;

    .img-part {
        width: 80px;
        height: 80px;

        .cart-img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 3px;
        }
    }

    .product-content {
        width: calc(100% - 80px - 8px);

        h5 {
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
        }

        .edit-icon {
            --Iconsax-Color: rgba(var(--title-color), 1);
            --Iconsax-Size: 16px;
        }

        .price {
            font-size: 16px;
            font-weight: 400;
            color: rgba(var(--title-color), 1);

            del {
                color: rgba(var(--content-color), 1);
            }
        }

        .plus-minus {
            display: inline-flex;
            align-items: center;
            text-align: center;
            padding: 4px 8px;
            background-color: rgba(var(--white), 1);
            border: 1px solid rgba(var(--border-color), 1);
            border-radius: 3px;

            .icon {
                --Iconsax-Size: 16px;
                --Iconsax-Color: rgba(var(--title-color), 1);
            }

            input {
                background-color: rgba(var(--white), 1);
                color: rgba(var(--theme-color), 1);
                border: none;
                font-size: 16px;
                font-weight: 400;
                line-height: 1;
                outline: none;
                width: 35px;
                text-align: center;
            }
        }
    }
}

.plant-product-box {
    padding: 8px;
    border: 1px solid rgba(var(--border-color), 1);
    border-radius: 10px;
    box-shadow: 0px 4px 11px 2px rgba(var(--black), 0.03);

    .img-part {
        border: 1px solid rgba(var(--border-color), 1);
        border-radius: 6px;

        .product {
            height: 125px;
            object-fit: cover;

            @media (max-width: 600px) {
                height: calc(100px + (125 - 100) * ((100vw - 320px) / (600 - 320)));
            }
        }

        .wishlist-btn {
            top: 5px;
            right: 5px;
        }
    }

    .product-content {
        .product-name {
            margin-top: 10px;
            font-weight: 700;
        }

        h6 {
            margin-top: 4px;
            font-size: 12px;
            font-weight: 500;
            color: rgba(var(--content-color), 1);
        }

        .price {
            font-weight: 700;
            font-size: 16px;
            color: rgba(var(--theme-color), 1);

            del {
                font-weight: 600;
                color: rgba(var(--content-color), 1);
            }
        }
    }
}

