.food-product-box {
    box-shadow: 15px 15px 30px 0px rgba(var(--bg-color), 1);
    border-radius: 16px;

    .food-rating {
        display: flex;
        gap: 5px;
        align-items: baseline;
        position: absolute;
        top: 10px;
        left: 10px;
