        background-color: rgba(var(--white), 1);
        border-radius: 100px;
        font-size: 12px;
        font-weight: 600;
        color: rgba(var(--dark-bg), 1);

        svg {
            @include pseudowh(10px, 10px);
            fill: rgba(var(--theme-color), 1);
        }
    }

    .f-rating {
        right: 12px;
        left: unset;
        top: 6px;
        background-color: rgba(var(--bg-color), 1);
        font-weight: 500;
    }

    .food-product-image {
        position: relative;

        img {
            height: 120px;
            object-fit: cover;
            border-top-left-radius: 16px;
            border-top-right-radius: 16px;
        }
    }

    .wishlist-btn {
        @include flex_common;
        position: absolute;
        top: -15%;
        right: 8%;
        padding: 6px;
        border-radius: 100%;
        box-shadow: 0 3px 15px 0 rgba(var(--black), 0.2);
        background-color: rgba(var(--theme-color), 1);

        [dir="rtl"] & {
            right: unset;
            left: 8%;
        }

        &.active,
        :active,
        :hover {
            border-color: transparent;
        }

        &:not(.btn-check)+.btn:active {
            border-color: transparent;
        }

        .icli {
            color: rgba(var(--white), 1);
        }

        .icbo {
            display: none;
        }

        &.active {
            .icli {
                display: none;
            }

            .icbo {
                display: flex;
                color: rgba(var(--white), 1);
            }

            .effect:before {
                animation: fireworkLine 0.5s linear 0.1s;
            }

            .effect:after {
                animation: fireworkPoint 0.5s linear 0.1s;
            }
        }
    }

    .fw-list {
        top: 12px;
        right: 15px;

    }

    .food-product-content {
        position: relative;
        padding: 13px;

        h5 {
            font-size: 14px;
            font-weight: 600;
            color: rgba(var(--dark-bg), 1);

        }

        .food-shipping {
            @include flex_common;
            gap: 10px;
            margin-top: 6px;

            .food-free-shipping {
                @include flex_common;
                gap: 6px;

                svg {
                    @include pseudowh (12px, 12px);
                    fill: rgba(var(--theme-color), 1);
                }

                h6 {
                    color: rgba(var(--content-color), 1);
                }
            }

        }

        .food-option {
            margin-top: 15px;
            display: flex;
            flex-wrap: wrap;
            gap: 6px;

            li {
                padding: 4px 8px;
                background-color: rgba(var(--bg-color), 1);
                border-radius: 5px;
                transition: all 0.2s ease-in-out;

                a {
                    color: rgba(var(--content-color), 1);
                    font-size: 12px;
                    font-weight: 500;
                }

                &:hover {
                    background-color: rgba(var(--theme-color), 1);

                    a {
                        color: rgba(var(--dark-bg), 1);
                    }
                }
            }
        }
    }
}


.food-product-wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    padding-bottom: 20px;
}

.shop-product-box {
    position: relative;
    box-shadow: 15px 15px 30px 0px rgba(var(--bg-color), 1);
    border-radius: 16px;

}

.food-product-image {
    img {
        height: 120px;
        object-fit: cover;
        border-top-left-radius: 16px;
        border-top-right-radius: 16px;
    }
}

.food-product-content {
    position: relative;
    padding: 12px;

    h5 {
        display: inline-flex;
        align-items: center;
        font-size: 14px;
        font-weight: 600;
        color: rgba(var(--dark-bg), 1);
    }

    span {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 13px;
        height: 13px;
        border: 1px solid rgba(var(--green), 1);
        border-radius: 3px;
        margin-left: 8px;

        &::before {
            content: '';
            width: 7px;
            height: 7px;
            background-color: rgba(var(--green), 1);
            border-radius: 50%;
            display: block;
        }

        [dir="rtl"] & {
            margin-left: unset;
            margin-right: 8px;
        }
    }

    .food-price {
        margin-top: 6px;
        font-size: 16px;
        font-weight: 600;
        color: rgba(var(--theme-color), 1);

        .price-number {
            border: none;
            font-weight: 500;
            color: rgba(var(--content-color), 1);
        }
    }

    .d-flex {
        p {
            font-weight: 500;
            color: rgba(var(--content-color), 1);

            button {
                border: none;
                font-weight: 600;
                background-color: transparent;
                color: rgba(var(--content-color), 1);
            }

        }

        .plus-minus {
            display: inline-flex;
            align-items: center;
            text-align: center;
            cursor: pointer;
            padding: 4px 8px;
            background-color: rgba(var(--white), 1);
            border: 1px solid rgba(var(--theme-color), 1);
            border-radius: 3px;

            .icon {
                --Iconsax-Size: 16px;
                --Iconsax-Color: rgba(var(--theme-color), 1);
            }

            input {
                width: 35px;
                background-color: rgba(var(--white), 1);
                color: rgba(var(--theme-color), 1);
                border: none;
                text-align: center;
                font-size: 15px;
                outline: none;
                font-weight: 500;
                line-height: 1;
            }
        }
    }

    .food-shipping {
        @include flex_common;
        gap: 10px;
        margin-top: 6px;

        .food-free-shipping {
            @include flex_common;
            gap: 6px;

            svg {
                @include pseudowh (15px, 15px);
                fill: rgba(var(--theme-color), 1);
            }

            h6 {
                color: rgba(var(--content-color), 1);
            }
        }

    }

    .food-option {
        display: flex;
        gap: 6px;
        margin-top: 12px;

        li {
            padding: 4px 8px;
            background-color: rgba(var(--bg-color), 1);
            border-radius: 5px;
            transition: all 0.2s ease-in-out;

            a {
                color: rgba(var(--content-color), 1);
                font-size: 12px;
                font-weight: 500;
            }

            &:hover {
                background-color: rgba(var(--theme-color), 1);

                a {
                    color: rgba(var(--dark-bg), 1);
                }
            }
        }
    }
}


.shop-product-rating {
    position: absolute;
    top: 10px;
    right: 8px;
    padding: 5px 7px 4px;
    background-color: rgba(var(--bg-color), 1);
    border-radius: 50px;

    [dir="rtl"] & {
        right: unset;
        left: 8px;
    }

    svg {
        margin-right: 2px;
        margin-bottom: 3px;
        @include pseudowh(12px, 12px);
        fill: rgba(var(--theme-color), 1);

        [dir="rtl"] & {
            margin-right: unset;
            margin-left: 2px;
        }
    }

}

.shop-rate {
    top: 35px;
    right: unset;
    left: 90px;

    [dir="rtl"] & {
        right: 90px;
        left: unset;
    }
}

.restaurant-rating {
    position: absolute;
    top: 10px;
    right: 8px;
    padding: 5px 7px 3px;
    background-color: rgba(var(--theme-color), 0.10);
    border-radius: 50px;

    [dir="rtl"] & {
        right: unset;
        left: 8px;
    }

    svg {
        margin-right: 2px;
        margin-bottom: 3px;
        @include pseudowh(12px, 12px);
        fill: rgba(var(--theme-color), 1);

        [dir="rtl"] & {
            margin-right: unset;
            margin-left: 2px;
        }
    }
}

.plus-minus {
    float: inline-end;
    margin-top: -16px;
    display: inline-flex;
    align-items: center;
    text-align: center;
    cursor: pointer;
    padding: 4px 8px;
    background-color: rgba(var(--white), 1);
    border: 1px solid rgba(var(--theme-color), 1);
    border-radius: 3px;

    .icon {
        --Iconsax-Size: 16px;
        --Iconsax-Color: rgba(var(--theme-color), 1);
    }

    input {
        width: 35px;
        background-color: rgba(var(--white), 1);
        color: rgba(var(--theme-color), 1);
        border: none;
        text-align: center;
        font-size: 15px;
        outline: none;
        font-weight: 500;
        line-height: 1;
    }
}

.food-detail-section {
    &:has(.restaurant-slider-detail .form-check .form-check-input:checked) {
        ~.food-product-section {
            .food-product-wrapper {
                &.non-veg {
                    display: grid;
                }

                &.veg {
                    display: none;
                }

                .food-product-content {
                    span {
                        border-color: rgba(var(--error-color), 1);

                        &::before {
                            background-color: rgba(var(--error-color), 1);
                        }
                    }
                }
            }
        }
    }
}

.food-product-section {
    .food-product-wrapper {
        &.veg {
            display: grid;
        }

        &.non-veg {
            display: none;
        }
    }
}


/* ====================
Hotel Booking card CSS 
=======================*/
.hotel-wishlist-card {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    margin-top: 18px;

    @media (max-width:374px) {
        grid-template-columns: 1fr;

    }
}

.hotel-card-box {
    box-shadow: 0px 2px 12px 0px rgba(var(--theme-color), 0.04);
    border-radius: 16px;
    border: 1px solid rgba(var(--theme-color), 0.1);

    .hotel-card-image {
        img {
            height: 182px;
            object-fit: cover;
            border-top-left-radius: 16px;
            border-top-right-radius: 16px;

            @media (max-width:600px) {
                height: calc(140px + (182 - 140) * ((100vw - 320px) / (600 - 320)));

            }
        }
    }

    .hotel-card-contant {
        position: relative;
        padding: 12px;

        .wishlist-btn {
            @include flex_common;
            position: absolute;
            top: -15%;
            right: 8%;
            padding: 6px;
            border-radius: 100%;
            background-color: rgba(var(--white), 1) !important;
            border: 1px solid rgba(var(--theme-color), 0.1);

            [dir="rtl"] & {
                right: unset;
                left: 8%;
            }

            .icli {
                color: rgba(var(--theme-color), 1);
            }

            .icbo {
                display: none;
            }

            &.active {
                .icli {
                    display: none;
                }

                .icbo {
                    display: flex;
                    color: rgba(var(--error-color), 1);
                }

                .effect:before {
                    animation: fireworkLine 0.5s linear 0.1s;
                }

                .effect:after {
                    animation: fireworkPoint 0.5s linear 0.1s;
                }
            }
        }

        h4 {
            @include text-ellipsis($line: 1);
            font-size: 14px;
            font-weight: 600;
            color: rgba(var(--title-color), 1);
        }

        h5 {
            @include text-ellipsis($line: 1);
            margin-top: 3px;
            font-size: 13px;
            font-weight: 400;
            color: rgba(var(--content-color), 1);

            .iconsax {
                --Iconsax-Color: rgba(var(--content-color), 1);
                --Iconsax-Size: 14px;
            }
        }

        .price-rate {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: 10px;

            h6 {
                font-size: 14px;
                font-weight: 700;
                color: rgba(var(--theme-color), 1);

                span {
                    font-weight: 600;
                    color: rgba(var(--content-color), 1);
                }

            }

            h5 {
                font-size: 13px;
                font-weight: 700;
                color: rgba(var(--title-color), 1);
            }
        }
    }
}