/*=====================
     Button CSS
=======================*/
.btn {
    padding: 12px;
    font-size: 16px;
    font-weight: 500;
    line-height: 1;
    border-radius: 6px;

    &-sm {
        padding: 5px 10px !important;
    }

    @media (max-width: 600px) {
        padding: calc(12px + (14 - 12) * ((100vw - 320px) / (600 - 320)));
        font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (600 - 320)));
    }

    &:active {
        color: rgba(255, 255, 255, 1) !important;
        background-color: rgba(var(--theme-color), 1) !important;
    }

    &:hover {
        color: rgba(255, 255, 255, 1);
        background-color: rgba(var(--theme-color), 1);
    }

    &.solid-btn {
        color: rgba(255, 255, 255, 1) !important;
        background-color: rgba(var(--theme-color), 1) !important;
    }

    &.outline-btn {
        color: rgba(var(--theme-color), 1) !important;
        background-color: transparent !important;
        border: 1px solid rgba(var(--theme-color), 1) !important;
    }

    &.rounded-btn {
        color: rgba(255, 255, 255, 1) !important;
        background-color: rgba(var(--theme-color), 1) !important;
        border-radius: 100px !important;
    }


}

/**** ecommerce button CSS ****/
.ecommerce-btn {
    font-weight: 600;
    color: rgba(255, 255, 255, 1);
    background-color: rgba(var(--theme-color), 1);
    text-transform: uppercase;

    &:hover {
        color: rgba(255, 255, 255, 1);
        background-color: rgba(var(--theme-color), 1) !important;
    }
}

/**** plant button CSS ****/
.plant-btn,
.pet-btn {
    font-weight: 700;
    font-size: 16px;
    color: rgba(255, 255, 255, 1);
    background-color: rgba(var(--theme-color), 1);
    text-transform: capitalize;
    border-radius: 100px;

    @media (max-width: 600px) {
        font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (600 - 320)));
    }

    &:hover {
        color: rgba(255, 255, 255, 1);
        background-color: rgba(var(--theme-color), 1) !important;
    }
}

.pet-btn {
    margin-top: 40px;
    text-transform: uppercase;
}

.plant-cart-btn {
    font-size: 12px;
    font-weight: 700;
    line-height: 1;
    padding: 6px 12px !important;
    color: rgba(255, 255, 255, 1);
    background-color: rgba(var(--theme-color), 1);

    &:hover {
        color: rgba(255, 255, 255, 1) !important;
        background-color: rgba(var(--theme-color), 1) !important;
    }
}

/**** grocery button CSS ****/
.grocery-btn {
    font-weight: 600;
    color: rgba(255, 255, 255, 1);
    background-color: rgba(var(--theme-color), 1);
    text-transform: uppercase;

    &:hover {
        color: rgba(255, 255, 255, 1);
        background-color: rgba(var(--theme-color), 1) !important;
    }
}


/***** food button ****/

.login-btn {
    text-align: center;
    font-size: 18px;
    font-weight: 600;
    margin-top: 44px;
    color: rgba(var(--dark-bg), 1);

    @media (max-width:600px) {
        margin-top: calc(20px + (44 - 20) * ((100vw - 320px) / (600 - 320)));
        font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (600 - 320)));
    }

}

.food-btn {
    position: sticky;
    width: 100%;
    font-weight: 500;
    padding: 14px;
    background-color: rgba(var(--theme-color), 1);
    border-radius: 8px;
    border: none;
    color: rgba(var(--dark-bg), 1);

    &:hover {
        color: rgba(var(--dak-bg), 1);
    }

}

.map-btn {
    margin-block: 20px;
    background-color: rgba(var(--bg-color), 1);
}

.m_top-30 {
    @media (max-width:600px) {
        margin-top: calc(12px + (30 - 12) * ((100vw - 320px) / (600 - 320)))
    }
}

.location-btn {
    margin-top: 20px;
    font-weight: 500;
    padding: 16px;
    background-color: rgba(var(--bg-color), 1);
    border-radius: 8px;
    color: rgba(var(--dark-bg), 1);
}



//******** chatting-button **********//
.chatting-btn {
    margin-top: 40px;
    font-size: 18px;
    font-weight: 600;
    color: rgba(var(--white), 1);
    background: var(--theme-color);
    border-radius: 8px;
    width: 100%;

    @media (max-width:600px) {
        font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (600 - 320)));
        margin-top: calc(35px + (40 - 35) * ((100vw - 320px) / (600 - 320)));
    }
}

.google-btn {
    margin-top: 23px;
    font-size: 14px;
    font-weight: 500;
    color: rgba(var(--title-color), 1);
    background-color: rgba(var(--white), 1);
    border: 1px solid rgba(var(--contant-color), 0.15);
    box-shadow: 0 2px 15px 0 rgba(var(--title-color), 0.08);

    @media (max-width:600px) {
        margin-top: calc(15px + (23 - 15) * ((100vw - 320px) / (600 - 320)));
    }

    &:hover {
        border: 1px solid rgba(var(--contant-color), 0.15) !important;
        color: rgba(var(--title-color), 1) !important;
    }
}

.chatting-fixed-button {
    position: fixed;
    bottom: 6px;
    left: 50%;
    transform: translate(-50%);
    max-width: 600px;
    padding: 10px 20px;
    background-color: rgba(var(--white), 1);
    width: 100%;

    a {
        margin-top: 0;
    }
}

/* ****** Hotel Booking CSS ****** */
.hotel-button {
    width: 100%;
    margin-top: 30px;
    font-size: 16px;
    font-weight: 600;
    color: rgba(var(--white), 1);
    background-color: rgba(var(--theme-color), 1);
    border-radius: 6px;
    margin-bottom: 18px;

    @media (max-width:600px) {
        font-size: calc(15px + (16 - 15) * ((100vw - 320px) / (600 - 320)));
        margin-top: calc(20px + (30 - 20) * ((100vw - 320px) / (600 - 320)));
        margin-bottom: calc(10px + (18 - 10) * ((100vw - 320px) / (600 - 320)));

    }
}

.download-button {
    position: fixed;
    left: 50%;
    transform: translate(-50%);
    width: 100%;
    max-width: 600px;
    padding: 12px 20px;
    bottom: 10px;
    background-color: rgba(var(--white), 1);

    a {
        width: 100%;
        font-size: 16px;
        font-weight: 600;
        color: rgba(var(--white), 1);
        background-color: rgba(var(--theme-color), 1);
        border-radius: 6px;

        @media (max-width:600px) {
            font-size: calc(15px + (16 - 15) * ((100vw - 320px) / (600 - 320)));
            margin-top: calc(20px + (30 - 20) * ((100vw - 320px) / (600 - 320)));

        }
    }
}

/************ Blog Button CSS *************/
.blog-fix-btn {
    background-color: rgba(var(--white), 1);
    padding: 18px 20px;
    position: fixed;
    width: 100%;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    max-width: 600px;
}

.blog-btn {
    width: 100%;
    padding-block: 12px;
    background-color: rgba(var(--theme-color), 1);
    color: rgba(var(--white), 1);
    font-size: 18px;
    font-weight: 500;
    text-align: center;
    border-radius: 8px;
}

/******** Learning Button CSS ********/
.learning-btn {
    width: 100%;
    background-color: rgba(var(--theme-color), 1);
    color: rgba(var(--white), 1);
    font-size: 18px;
    font-weight: 600;
    text-align: center;
    border-radius: 30px;
    padding-block: 15px;

    @media (max-width:600px) {
        font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (600 - 320)));
        padding-block: calc(12px + (15 - 12) * ((100vw - 320px) / (600 - 320)));

    }

}

.learn-skip {
    text-align: center;
    font-size: 16px;
    font-weight: 600;
    color: rgba(var(--title-color), 1);
    display: block;
    margin-top: 20px;

    @media (max-width:600px) {
        margin-top: calc(15px + (20 - 15) * ((100vw - 320px) / (600 - 320)));
    }
}

.learn-account {
    margin-top: 15px;
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    color: rgba(var(--content-color), 1);

    @media (max-width:600px) {
        margin-top: calc(10px + (15 - 10) * ((100vw - 320px) / (600 - 320)));
    }

    a {
        font-weight: 600;
        color: rgba(var(--title-color), 1);
    }
}