/*=====================
     Auth CSS
=======================*/
/*<================== ecommerce auth =================>*/
.ecommerce-auth-title {
     position: relative;
     text-align: center;
     padding-top: 65px;

     .logo {
          width: 150px;

          @media (max-width: 600px) {
               width: calc(120px + (150 - 120) * ((100vw - 320px) / (600 - 320)));
          }
     }

     h2 {
          margin-top: 16px;
          font-size: 24px;
          font-weight: 700;
          color: rgba(var(--title-color), 1);

          @media (max-width: 600px) {
               font-size: calc(20px + (24 - 20) * ((100vw - 320px) / (600 - 320)));
          }
     }

     h3 {
          margin-top: 8px;
          font-size: 18px;
          font-weight: 400;
          padding-inline: 70px;
          width: 100%;
          color: rgba(var(--content-color), 1);

          @media (max-width: 600px) {
               font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (600 - 320)));
               padding-inline: calc(40px + (70 - 40) * ((100vw - 320px) / (600 - 320)));
          }
     }

     p {
          margin-top: 10px;
          font-size: 15px;
          font-weight: 400;
          color: rgba(var(--content-color), 1);
     }

     .lemon-img {
          position: absolute;
          right: 50px;
          top: 50px;
     }


     .apple-img {
          position: absolute;
          left: 30px;
          bottom: -45px;
     }

     .leaf-img {
          position: absolute;
          right: 30px;
          bottom: -50px;
          animation: vector 1s infinite alternate;
     }
}

/*<================== plant auth =================>*/

.plant-auth-title {
     margin-top: 30px;

     h2 {
          font-size: 22px;
          font-weight: 700;
          color: rgba(var(--title-color), 1);
     }

     h3 {
          margin-top: 4px;
          font-size: 14px;
          font-weight: 500;
          color: rgba(var(--content-color), 1);
     }
}

/*<================== grocery auth =================>*/
.back-link {
     font-weight: 500;
     color: rgba(var(--content-color), 1);

     .back-icon {
          --Iconsax-Color: rgba(var(--content-color), 1);
          --Iconsax-Size: 20px;
     }
}

/*<================== pet auth =================>*/
.pet-auth-title {
     position: relative;
     text-align: center;
     padding-top: 42px;

     h2 {
          font-size: 24px;
          font-weight: 700;
          color: rgba(var(--title-color), 1);

          @media (max-width: 600px) {
               font-size: calc(20px + (24 - 20) * ((100vw - 320px) / (600 - 320)));
          }
     }

     h3 {
          margin-top: 8px;
          font-size: 18px;
          font-weight: 400;
          padding-inline: 70px;
          width: 100%;
          color: rgba(var(--content-color), 1);

          @media (max-width: 600px) {
               font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (600 - 320)));
               padding-inline: calc(40px + (70 - 40) * ((100vw - 320px) / (600 - 320)));
          }
     }

     p {
          margin-top: 10px;
          font-size: 15px;
          font-weight: 400;
          color: rgba(var(--content-color), 1);
     }
}

.google-pet-buttons {
     display: flex;
     flex-direction: column;
     gap: 10px;

     a {
          font-weight: 500;
          font-size: 14px;
          color: rgba(var(--title-color), 1);
          @include flex_center;
          gap: 5px;
          padding: 14px;

          .icon {
               width: 20px;
          }

          &:hover {
               color: rgba(var(--title-color), 1) !important;
               background-color: rgba(var(--light-bg), 1) !important;
          }

     }
}


/*<================== Food auth =================>*/

.food-auth-title {
     margin-top: 10px;

     h2 {
          font-weight: 600;
          color: rgba(var(--dark-bg), 1);

          @media (max-width:600px) {
               font-size: calc(22px + (24 - 22) * ((100vw - 320px) / (600 - 320)));
          }
     }

     p {
          line-height: 1.5;
          font-size: 18px;
          font-weight: 400;
          color: rgba(var(--content-color), 1);

          @media (max-width:600px) {
               font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (600 - 320)));

          }
     }
}

.food-form {
     label {
          color: rgba(var(--dark-bg), 1);
          font-size: 16px;
          font-weight: 600;
     }

     input {
          background-color: transparent;
          width: 100%;
          border: none;
          font-size: 14px;
          font-weight: 500;
          color: rgba(var(--title-color), 1);
          padding-right: 10px;

          [dir="rtl"] & {
               padding-left: 10px;
          }

          &:focus-visible {
               outline: none;
          }
     }

     .food-dropdown {
          display: flex;
          align-items: center;
          margin-top: 10px;
          background-color: rgba(var(--bg-color), 1);
          border-radius: 6px;

          a {
               display: flex;

               img {
                    margin-right: 5px;
               }

               .icon {
                    --Iconsax-Color: rgba(var(--dark-bg), 1);
                    --Iconsax-Size: 20px;
               }
          }
     }
}

.food-social-icon {
     display: flex;
     justify-content: center;
     gap: 35px;
     padding-block: 20px;

     @media (max-width:600px) {
          gap: calc(20px + (35 - 20) * ((100vw - 320px) / (600 - 320)));
          padding-block: calc(12px + (20 - 12) * ((100vw - 320px) / (600 - 320)));

     }

     li {
          background-color: rgba(var(--bg-color), 1);
          border-radius: 14px;

          a {
               @include flex_center;
               @include pseudowh (calc(50px + (54 - 50) * ((100vw - 320px) / (600 - 320))), calc(50px + (54 - 50) * ((100vw - 320px) / (600 - 320))));
               padding: 14px;
          }


     }
}

/*==================
Food OTP auth 
====================*/
.starting-heading {
     text-align: center;
     margin-top: 10px;

     p {
          font-size: 16px;
          font-weight: 500;
          color: rgba(var(--content-color), 1);
          margin-bottom: 10px;
     }

     h4 {
          color: rgba(var(--dark-bg), 1);
          font-weight: 600;
          font-size: 16px;
          margin-bottom: 25px;
     }
}

.otp-request {
     text-align: center;
     padding-block: 40px 60px;

     @media (max-width:600px) {
          padding-block: calc(30px + (60 - 30) * ((100vw - 320px) / (600 - 320)));
     }

     p {
          font-size: 16px;
          font-weight: 500;
          color: rgba(var(--content-color), 1);

          a {
               font-size: 16px;
               font-weight: 500;
               color: rgba(var(--dark-bg), 1);
               transition: all 0.3s;

               &:hover {
                    color: rgba(var(--theme-color), 1);
                    transition: all 0.3s ease-in-out;

               }
          }
     }

}

/*<================== Chatting authentication =================>*/
.chatting-authentication-section {
     background: var(--theme-color);
     height: 100vh;
     z-index: 0;

     .chatting-circle {
          width: 100%;
          height: 100%;
          position: absolute;
          display: flex;
          justify-content: center;
          padding-top: 30px;

          .circle-1 {
               width: 600px;
               height: 600px;
               border-radius: 1000px 1000px 0 0;
               border: 1px solid rgba(var(--white), 0.2);
               border-bottom: 0;
               transition: all .4s ease-in-out;
               position: absolute;
               top: 30px;
          }

          .circle-2 {
               position: absolute;
               top: 70px;
               width: 498px;
               height: 372px;
               border-radius: 1000px 1000px 0 0;
               border: 1px solid rgba(var(--white), 0.2);
               border-bottom: 0;
               transition: all .4s ease-in-out;
          }

          .circle-3 {
               width: 398px;
               height: 371px;
               border-radius: 1000px 1000px 0 0;
               border: 1px solid rgba(var(--white), 0.2);
               border-bottom: 0;
               transition: all .4s ease-in-out;
               position: absolute;
               top: 113px;
          }

          .circle-4 {
               width: 339px;
               height: 172px;
               top: 153px;
               border-radius: 1000px 1000px 0 0;
               border: 1px solid rgba(var(--white), 0.2);
               border-bottom: 0;
               position: absolute;
               transition: all .4s ease-in-out;
          }
     }

     .chatting-top-box {
          position: relative;

          .chatting-top-profile {
               display: flex;
               position: relative;
               z-index: 1;

               .person-image1 {
                    @include pseudowh(70px, 70px);
                    outline: 2px solid rgba(var(--white), 0.2);
                    outline-offset: 4px;
                    border-radius: 100px;
                    margin-top: 50px;
                    margin-inline: auto 46px;

                    @media (max-width:600px) {
                         @include pseudowh(calc(60px + (70 - 60) * ((100vw - 320px) / (600 - 320))), calc(60px + (71 - 60) * ((100vw - 320px) / (600 - 320))));
                         margin-inline: auto calc(10px + (44 - 10) * ((100vw - 320px) / (600 - 320)));
                    }
               }

               .person-image3 {
                    @include pseudowh(58px, 58px);
                    outline: 2px solid rgba(var(--white), 0.2);
                    outline-offset: 4px;
                    border-radius: 100px;
                    margin-top: 50px;
                    margin-inline: auto 44px;

                    @media (max-width:600px) {
                         @include pseudowh(calc(50px + (58 - 50) * ((100vw - 320px) / (600 - 320))), calc(50px + (58 - 50) * ((100vw - 320px) / (600 - 320))));
                         margin-inline: auto calc(40px + (44 - 40) * ((100vw - 320px) / (600 - 320)));
                    }
               }

               .left-chat-box {
                    position: absolute;
                    right: 100px;
                    top: 32px;
                    animation: bonus 2s ease-in-out infinite alternate;

                    @media (max-width:600px) {
                         right: calc(50px + (100 - 50) * ((100vw - 320px) / (600 - 320)));
                         top: calc(8px + (32 - 8) * ((100vw - 320px) / (600 - 320)));
                    }
               }

               .top-whatsapp-icon {
                    position: absolute;
                    top: 30px;
                    right: 18px;
                    animation: bonus 2s ease-in-out infinite alternate;

                    @media (max-width:600px) {
                         right: calc(8px + (18 - 8) * ((100vw - 320px) / (600 - 320)));
                    }
               }
          }

          div {
               position: relative;
               z-index: 1;

               .person-image2 {
                    @include pseudowh(58px, 58px);
                    outline: 2px solid rgba(var(--white), 0.2);
                    outline-offset: 4px;
                    border-radius: 100px;
                    margin-left: 75px;
                    margin-top: -20px;

                    @media (max-width:600px) {
                         @include pseudowh(calc(50px + (58 - 50) * ((100vw - 320px) / (600 - 320))), calc(50px + (58 - 50) * ((100vw - 320px) / (600 - 320))));
                         margin-left: calc(20px + (75 - 20) * ((100vw - 320px) / (600 - 320)));
                    }
               }

               .person-image4 {
                    @include pseudowh(70px, 70px);
                    outline: 2px solid rgba(var(--white), 0.2);
                    outline-offset: 4px;
                    border-radius: 100px;
                    margin-left: 67px;
                    margin-top: -20px;

                    @media (max-width:600px) {
                         @include pseudowh(calc(60px + (70 - 60) * ((100vw - 320px) / (600 - 320))), calc(60px + (71 - 60) * ((100vw - 320px) / (600 - 320))));
                         margin-left: calc(20px + (67 - 20) * ((100vw - 320px) / (600 - 320)));
                    }
               }

               .top-chat-box {
                    margin-top: -60px;
                    margin-left: -22px;
                    animation: bonus 2s ease-in-out infinite alternate;
               }
          }

          .login-details-box {
               z-index: 1;
               position: fixed;
               width: 100%;
               background-color: rgba(var(--white), 1);
               border-radius: 30px 30px 0 0;
               bottom: 0;
               max-width: 600px;
               left: 50%;
               transform: translate(-50%);
               padding-inline: 20px;
               height: calc(100vh - 190px);
               padding-bottom: 20px;
               overflow: auto;
               scrollbar-width: none;

               ::-webkit-scrollbar {
                    width: 0px;
                    background: transparent;
               }


               .login-welcome-content {
                    padding-top: 28px;

                    h2 {
                         color: rgba(var(--title-color), 1);
                         font-size: 22px;
                         font-weight: 700;

                         @media (max-width:600px) {
                              font-size: calc(20px + (22 - 20) * ((100vw - 320px) / (600 - 320)));
                         }
                    }

                    p {
                         color: rgba(var(--content-color), 1);
                         font-size: 16px;
                         font-weight: 400;
                         margin-top: 3px;

                         @media (max-width:600px) {
                              font-size: calc(14px + (16 - 20) * ((100vw - 320px) / (600 - 320)));
                         }
                    }
               }

               .text-signup {
                    margin-top: 15px;
                    font-size: 16px;
                    font-weight: 400;
                    text-align: center;
                    color: rgba(var(--content-color), 1);

                    @media (max-width:600px) {
                         font-size: calc(15px + (16 - 15) * ((100vw - 320px) / (600 - 320)));
                    }

                    a {
                         font-weight: 500;
                         color: rgba(var(--title-color), 1);
                    }
               }

               .division2 {
                    position: relative;
                    text-align: center;
                    margin-top: 23px;

                    @media (max-width:600px) {
                         margin-top: calc(10px + (23 - 10) * ((100vw - 320px) / (600 - 320)));
                    }

                    span {
                         position: relative;
                         font-size: 14px;
                         font-weight: 400;
                         background-color: rgba(var(--white), 1);
                         color: rgba(var(--content-color), 1);
                         padding: 0 10px;
                    }

                    &::after {
                         content: "";
                         position: absolute;
                         top: 50%;
                         left: 50%;
                         transform: translateX(-50%);
                         width: 80px;
                         background: linear-gradient(270deg, rgba(155, 166, 184, 0) -2.05%, rgb(155, 166, 184) 50.93%, rgba(155, 166, 184, 0) 101.74%);
                         z-index: -1;
                         height: 2px;
                    }
               }
          }

          .sign-box {
               height: 100%;
               max-height: calc(100vh - 187px);
          }
     }
}




/* ============== Blog auth page =============*/
.blog-auth-form {
     .form-group {
          position: relative;
          margin-top: 38px;

          @media (max-width:600px) {
               margin-top: calc(15px + (38 - 15) * ((100vw - 320px) / (600 - 320)));
          }

          +.form-group {
               margin-top: 20px;

               @media (max-width:600px) {
                    margin-top: calc(15px + (20 - 15) * ((100vw - 320px) / (600 - 320)));
               }
          }

          .form-check-label {
               font-size: 16px;
               font-weight: 600;
               color: rgba(var(--title-color), 1);

               @media (max-width:600px) {
                    font-size: calc(15px + (16 - 15) * ((100vw - 320px) / (600 - 320)));
               }
          }

          .form-control {
               position: relative;
               font-size: 14px;
               font-weight: 500;
               padding: 14px 16px;
               background-color: rgba(var(--content-color), 0.08);
               color: rgba(var(--content-color), 1);
               border: none;
               border-radius: 8px;
               -webkit-text-security: none;
               margin-top: 6px;

               &::placeholder {
                    color: rgba(var(--content-color), 1);
                    font-weight: 400;
               }

               @media (max-width: 600px) {
                    padding: calc(12px + (16 - 12) * ((100vw - 320px) / (600 - 320)));
               }

               body.dark & {
                    background-color: rgba(var(--light-bg), 1) !important;
               }

               &.search {
                    padding-block: 12px;
                    padding-inline: 40px;
                    font-size: 14px;
                    font-weight: 400;
                    background-color: rgba(var(--content-color), 0.05);
                    z-index: 0;
                    margin-top: 0;

                    @media (max-width: 600px) {
                         font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (600 - 320)));
                    }
               }

               &.comments {
                    padding-block: 12px;
                    padding-inline: 17px 12px;
                    font-size: 14px;
                    font-weight: 400;
                    background-color: rgba(var(--content-color), 0.05);
                    z-index: 0;
                    margin-top: 0;

                    @media (max-width: 600px) {
                         font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (600 - 320)));
                    }
               }

               &.textarea-form-control {
                    border-radius: 8px !important;
               }

               &::placeholder {
                    color: rgba(var(--content-color), 1);
                    font-weight: 400 !important;
               }
          }

          .search-icon {
               position: absolute;
               top: 50%;
               left: 5px;
               transform: translateY(-50%);
               --Iconsax-Color: rgba(var(--content-color), 1);
               --Iconsax-Size: 20px;
               z-index: 2;
               background-color: transparent;

               [dir="rtl"] & {
                    left: unset;
                    right: 10px;
               }
          }

          .mic-icon {
               position: absolute;
               top: 50%;
               right: 10px;
               transform: translateY(-50%);
               --Iconsax-Color: rgba(var(--theme-color), 1);
               --Iconsax-Size: 24px;

               [dir="rtl"] & {
                    right: unset;
                    left: 10px;
               }
          }

          .send-icon {
               position: absolute;
               top: 50%;
               right: 10px;
               transform: translateY(-50%);
               --Iconsax-Color: rgba(var(--content-color), 1);
               --Iconsax-Size: 19px;

               [dir="rtl"] & {
                    right: unset;
                    left: 10px;
               }
          }

          .reset {
               float: right;
               margin-top: 10px;
               font-size: 14px;
               font-weight: 500;
               color: rgba(var(--theme-color), 1);
          }

     }

     .blog-from {
          margin-top: 15px;
     }
}

.account-section {
     font-size: 16px;
     font-weight: 600;
     margin-top: 10px;
     text-align: center;
     color: rgba(var(--content-color), 1);

     h4 {

          a {
               color: rgba(var(--theme-color), 1);
          }
     }

     h6 {
          position: relative;
          margin-top: 25px;

          &::after {
               @include pos;
               top: -5px;
               left: 50%;
               transform: translateX(-50%);
               height: 1px;
               width: 23px;
               background-color: rgba(var(--theme-color), 1);
          }
     }

     h5 {
          text-align: center;
          margin-top: 117px;
          color: rgba(var(--content-color), 1);
          font-size: 16px;
          font-weight: 400;
          line-height: 1.5;

          @media (max-width:600px) {
               margin-top: calc(85px + (117 - 85) * ((100vw - 320px) / (600 - 320)));
          }
     }

     ul {
          margin-top: 15px;
          @include flex_center;
          gap: 20px;

          @media (max-width:600px) {
               gap: calc(12px + (20 - 12) * ((100vw - 320px) / (600 - 320)));

          }

          li {

               a {
                    @include flex_center;
                    @include pseudowh(64px, 64px);
                    border-radius: 100px;
                    background-color: rgba(var(--content-color), 0.2);

                    @media (max-width:600px) {
                         @include pseudowh(calc(55px + (64 - 55) * ((100vw - 320px) / (600 - 320))), calc(55px + (64 - 55) * ((100vw - 320px) / (600 - 320))))
                    }
               }
          }
     }
}

/* =========== learning auth CSS ===========*/
.learning-login-section {
     background-color: rgba(var(--white), 1);
     overflow: hidden;
     padding-top: 0;
     padding-bottom: 21px;
     border-bottom-left-radius: 20px;
     border-bottom-right-radius: 20px;
     box-shadow: -1px 1px 0px 1px rgba(var(--black), 0.08);
     position: relative;

     .login-header-img {
          display: flex;
          justify-content: end;
          padding-bottom: 20px;

          .image1 {
               position: absolute;
               top: 30px;
               right: 12px;
          }

          .image2 {
               margin-top: -32px;
               margin-right: -12px;
          }
     }

}

.learning-auth-form {
     margin-top: 30px;

     @media (max-width:600px) {
          margin-top: calc(15px + (30 - 15) * ((100vw - 320px) / (600 - 320)));
     }

     .form-group {
          margin-top: 0;

          .learn-select {
               width: auto;
               box-shadow: 2px 2px 0 0 rgba(var(--title-color), 1);
          }

          +.form-group {
               margin-top: 20px;

               @media (max-width:600px) {
                    margin-top: calc(15px + (20 - 15) * ((100vw - 320px) / (600 - 320)));
               }
          }

          .reset {
               float: right;
               margin-top: 14px;
               font-size: 14px;
               font-weight: 400;
               color: rgba(var(--secondary-color), 1);
          }

          .form-control {
               position: relative;
               padding: 15px 35px 15px 45px;
               font-size: 14px;
               font-weight: 400;
               line-height: 1;
               color: rgba(var(--title-color), 1);
               background-color: rgba(var(--content-color), 0.1);
               border-radius: 8px;
               border: none;
               box-shadow: 2px 2px 0 0 rgba(var(--title-color), 1);

               [dir="rtl"] & {
                    text-align: right;
               }

               body.dark & {
                    background-color: rgba(var(--light-bg), 1) !important;
               }

               &:focus {
                    border: none;
                    color: rgba(var(--title-color), 1);
               }

               &::placeholder {
                    color: rgba(var(--title-color), 1);
               }

               &.number-control {
                    padding-left: 20px;
               }

               &.search {
                    padding-block: 15px;
                    padding-right: 50px;
                    font-size: 14px;
                    font-weight: 400;
                    background-color: rgba(var(--content-color), 0.05);
               }
          }

          .icon {
               position: absolute;
               top: 50%;
               left: 15px;
               transform: translateY(-50%);
               --Iconsax-Color: rgba(var(--title-color), 1);
               --Iconsax-Size: 20px;

               [dir="rtl"] & {
                    right: 15px;
                    left: unset;
               }
          }
     }

     .flex-spacing {
          display: flex;
          align-items: center;
          justify-content: space-between;

          .custom-checkbox {
               display: flex;
               align-items: center;
               gap: 10px;
               font-size: 14px;
               font-weight: 400;
               color: rgba(var(--title-color), 1);
               cursor: pointer;
               margin-top: 5px;

               input {
                    display: none;
               }

               .checkmark {
                    @include pseudowh(20px, 20px);
                    margin: 0;
                    border-radius: 4px;
                    display: inline-block;
                    position: relative;
                    border: 1px solid rgba(var(--theme-color), 0.3);
                    transition: background-color 0.3s, border-color 0.3s;
               }

               input:checked+.checkmark {
                    background-color: rgba(var(--theme-color), 1);
                    border-color: rgba(var(--theme-color), 1);
               }

               input:checked+.checkmark::after {
                    @include pos;
                    @include pseudowh(6px, 12px);
                    top: 1px;
                    left: 6px;
                    border: solid rgba(var(--white), 1);
                    border-width: 0 2px 2px 0;
                    transform: rotate(45deg);
               }
          }

          a {
               color: rgba(var(--theme-color), 1);
               font-size: 13px;
               font-weight: 500;
          }
     }

     .search-icon {
          position: absolute;
          top: 50%;
          left: 6px;
          transform: translateY(-50%);
          --Iconsax-Color: rgba(var(--title-color), 1);
          --Iconsax-Size: 20px;

          [dir="rtl"] & {
               left: unset;
               right: 10px;
          }
     }

     .filter-btn {
          position: absolute;
          top: 50%;
          right: 10px;
          transform: translateY(-50%);
          padding-left: 10px;
          border-left: 1px solid rgba(210, 210, 210, 1);
          --Iconsax-Color: rgba(var(--title-color), 1);
          --Iconsax-Size: 20px;
          border-radius: 0;
          height: 30px;

          [dir="rtl"] & {
               right: unset;
               left: 10px;
               border-left: unset;
               border-right: 1px solid rgba(210, 210, 210, 1);
               padding-left: unset;
               padding-right: 5px;
          }
     }
}

.login-social-list {
     padding-top: 15px;

     @media (max-width:420px) {
          padding-top: 0;
     }

     .division2 {
          position: relative;
          text-align: center;

          span {
               font-size: 14px;
               font-weight: 400;
               background-color: rgba(var(--white), 1);
               color: rgba(var(--content-color), 1);
               padding: 0 6px;
          }

          &::after {
               @include pos;
               width: 32%;
               height: 1px;
               top: 50%;
               left: 50%;
               transform: translateX(-50%);
               background-color: rgba(var(--content-color), 1);
               z-index: -1;
          }

     }

     .auth-social-links {
          padding-top: 43px;
          display: flex;
          flex-wrap: wrap;
          gap: 13px;
          justify-content: center;

          @media (max-width:600px) {
               padding-top: calc(25px + (43 - 25) * ((100vw - 320px) / (600 - 320)));
               gap: calc(8px + (13 - 8) * ((100vw - 320px) / (600 - 320)));
          }

          li {
               a {
                    width: 100%;
                    @include flex_center;
                    font-size: 14px;
                    font-weight: 400;
                    color: rgba(var(--title-color), 1);
                    background-color: rgba(var(--white), 1);
                    border-radius: 5px;
                    box-shadow: 0px 0px 3px 0px rgba(var(--black), 0.1);
                    padding: 12px;
                    gap: 6px;

                    @media (max-width:600px) {
                         padding-block: calc(12px + (10 - 12) * ((100vw - 320px) / (600 - 320)));
                    }

                    img {
                         @include pseudowh(25px, 25px);
                    }

               }
          }
     }
}