.learning {
    background-color: rgba(247, 248, 248, 1);
    max-width: 600px;
    width: 100%;
}




/* ================
Home page CSS
===================*/
.learn-category-section {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 15px;
    margin-top: 12px;
    overflow-x: auto;
    scrollbar-width: none;

    li {
        h5 {
            margin-top: 10px;
            text-align: center;
            font-size: 14px;
            font-weight: 400;
            color: rgba(var(--title-color), 1);
        }

        a {
            @include flex_center;
            @include pseudowh(65px, 60px);
            background-color: rgba(var(--white), 1);
            border-radius: 8px;
            box-shadow: 2px 2px 0 0 rgba(var(--theme-color), 1);
        }

        &:nth-child(2) a {
            box-shadow: 2px 2px 0 0 rgba(var(--secondary-color), 1);
        }

        &:nth-child(3) a {
            box-shadow: 2px 2px 0 0 rgba(var(--light-blue), 1);
        }

        &:nth-child(4) a {
            box-shadow: 2px 2px 0 0 rgba(248, 217, 158, 1);
        }
    }
}


.learn-main--image {
    border-radius: 12px;
}

/* =================
courses section CSS 
====================*/
.courses-slider-box {
    border: 1px solid rgba(var(--content-color), 0.2);
    background-color: rgba(var(--light-bg), 1);
    border-radius: 10px;
    padding: 8px;
    margin-top: 10px;

    img {
        position: relative;
        width: 100%;
        object-fit: cover;
        border-radius: 10px;
        height: 107px;
    }

    .wishlist-btn {
        position: absolute;
        top: 5px;
        right: 10px;
        padding: 6px;
        border-radius: 100%;
        border: none;

        &:hover {
            background-color: transparent !important;
        }

        [dir="rtl"] & {
            right: unset;
            left: 8%;
        }

        .icli {
            color: rgba(var(--white), 1);
            font-size: 20px;
        }

        .icbo {
            display: none;
        }

        &.active {
            .icli {
                display: none;
            }

            .icbo {
                display: flex;
                color: rgba(var(--white), 1);
                font-size: 20px;
            }
        }
    }

    .course-detail {
        padding: 8px 8px 0px 8px;

        h3 {
            display: flex;
            align-items: flex-end;
            font-size: 13px;
            font-weight: 500;
            color: rgba(var(--content-color), 1);
            gap: 3px;

            .icon {
                --Iconsax-Color: rgba(var(--black), 1);
                --Iconsax-Size: 16px;
            }
        }

        h4 {
            margin-top: 5px;
            font-size: 14px;
            font-weight: 500;
            color: rgba(var(--title-color), 1);
        }

        .course-price {
            display: flex;
            align-items: center;
            justify-content: space-between;

            span {
                margin-top: 4px;
                font-size: 13px;
                font-weight: 500;
                color: rgba(var(--theme-color), 1);

                del {
                    color: rgba(var(--content-color), 1);
                    color: rgba(var(--content-color), 1);
                    margin-left: 5px;
                    font-weight: 600;
                }
            }

            h4 {
                display: flex;
                align-items: center;
                gap: 3px;
                color: rgba(var(--title-color), 1);
                font-size: 12px;
                font-weight: 400;

                img {
                    @include pseudowh(14px, 14px);
                }
            }

        }
    }
}

/* ============
coach section
================*/
.coach-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 13px;
    overflow-x: auto;
    scrollbar-width: none;
    margin-top: 10px;

    li {
        a {
            img {
                @include pseudowh(66px, 66px);
                border-radius: 100%;
            }
        }

        h4 {
            text-align: center;
            margin-top: 5px;
            font-size: 13px;
            font-weight: 500;
            color: rgba(var(--title-color), 1);
        }
    }
}

/* =============
learning CSS 
=================*/
.learning-wrapper {
    margin-top: 13px;

    li {
        width: 100%;

        +li {
            margin-top: 15px;
        }

        a {
            display: flex;
            align-items: center;
            gap: 10px;
            background-color: rgba(var(--white), 1);
            border-radius: 10px;
            padding: 12px;
            box-shadow: 0 0 4px 0 rgba(var(--black), 0.09);

            img {
                @include pseudowh(73px, 65px);
                border-radius: 5px;
            }

            .learning-list-content {
                width: calc(100% - 10px - 73px);

                .coaching-list {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;

                    h3 {
                        display: flex;
                        align-items: flex-end;
                        gap: 4px;
                        font-size: 13px;
                        font-weight: 500;
                        color: rgba(var(--content-color), 1);

                        .icon {
                            --Iconsax-Color: rgba(var(--title-color), 1);
                            --Iconsax-Size: 16px;
                        }
                    }

                    h4 {
                        font-size: 12px;
                        font-weight: 400;
                        color: rgba(var(--secondary-color), 1);
                        background-color: rgba(var(--secondary-color), 0.13);
                        border-radius: 25px;
                        padding: 4px 12px;
                    }
                }

                h6 {
                    font-size: 14px;
                    font-weight: 500;
                    color: rgba(var(--title-color), 1);
                }

                .reading-view {
                    h6 {
                        text-align: end;
                        font-size: 12px;
                        font-weight: 500;
                        color: rgba(var(--title-color), 1);
                    }

                    .progress {
                        margin-top: 5px;
                        height: 5px;
                        border-radius: 50px;

                        .progress-bar {
                            border-radius: 50px;
                            background-color: rgba(var(--theme-color), 1);

                        }
                    }
                }
            }
        }

        &:nth-child(1) .progress-bar {
            width: 50%;
        }

        &:nth-child(2) .progress-bar {
            width: 30%;
        }

        &:nth-child(3) .progress-bar {
            width: 80%;
        }
    }
}