/* ================
chat section CSS 
====================*/
.message-chat-box {
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    max-width: 600px;
    z-index: 2;
    padding: 16px 20px;
    background-color: rgba(var(--white), 1);

    @media (max-width:600px) {
        padding-inline: calc(12px + (20 - 12) * ((100vw - 320px) / (600 - 320)));
    }

    .chat-input {
        position: relative;
        width: 100%;
        padding-block: 12px;
        padding-right: calc(100% - (100% - 120px));
        padding-left: 35px;
        color: rgba(var(--title-color), 1);
        border: 1px solid rgba(var(--content-color), 0.15);
        box-shadow: 0 2px 15px 0 rgba(var(--black), 0.08);
        border-radius: 8px;
        outline: none;
        background-color: rgba(var(--white), 1);
    }

    .smile {
        @include pseudowh(26px, 26px);
        position: absolute;
        top: 50%;
        left: 25px;
        transform: translateY(-50%);
        cursor: pointer;

        @media (max-width:600px) {
            left: calc(18px + (25 - 18) * ((100vw - 320px) / (600 - 320)));
        }
    }

    .chat-message-icon {
        @include pseudowh(24px, 24px);
        position: absolute;
        top: 50%;
        right: 20%;
        gap: 16px;
        display: flex;
        align-items: center;
        --Iconsax-Color: rgba(var(--content-color), 1);
        --Iconsax-Size: 20px;
        transform: translateY(-50%);
        cursor: pointer;

        @media (max-width:375px) {
            right: 33%;

        }
    }

    .chat-icon {
        right: 8%;
    }

    .input-icon {
        display: flex;
        align-items: center;
        gap: 15px;
        cursor: pointer;
        font-size: 18px;
        color: rgba(var(--content-color), 1);

        @media (max-width:375px) {
            right: 8%;
        }

        [dir="rtl"] & {
            right: unset;
            left: 14%;
        }

        .chat-icon {
            position: absolute !important;
            transform: translate(80%, -40px) !important;
            border: none;
            background-color: transparent;
            margin-top: 20px;

            [dir="rtl"] & {
                transform: translate(-77%, -40px) !important;

            }

            .dropdown-item {
                @include pseudowh(47px, 47px);
                @include flex_center;
                border-radius: 50%;
                background-color: rgba(var(--white), 1);
                opacity: 1;
                box-shadow: -2px 6px 8px 0px rgba(var(--bg-color), 1);
                border: none;

                &:hover {
                    background: var(--theme-color);

                    .clip-icon {
                        --Iconsax-Color: rgba(var(--white), 1);
                    }
                }
            }
        }
    }

    .send-btn {
        @include flex_center;
        background: var(--theme-color);
        border: none;
        padding: 8px;
        border-radius: 12px;
        cursor: pointer;

        @media (max-width:600px) {
            padding: calc(6px + (8 - 6) * ((100vw - 320px) / (600 - 320)));
        }

        .send-icon {
            --Iconsax-Color: rgba(var(--white), 1);
            --Iconsax-Size: 24px;

            @media (max-width:600px) {
                --Iconsax-Size: calc(20px + (24 - 20) * ((100vw - 320px) / (600 - 320)));

            }
        }
    }
}

.chatting-section {
    height: 100%;
    margin-top: -30px;
    border-radius: 30px 30px 0 0;
    background-color: rgba(var(--white), 1);
    overflow: unset;
    z-index: 1;
    position: relative;
    scrollbar-width: none;

    ::-webkit-scrollbar {
        width: 0px;
        background: transparent;
    }

    .chatting-scroll {
        height: calc(100vh - 115px);
        overflow: auto;

        &:has(.message-chat-box) {
            height: calc(100vh - 185px);
        }

        .message-box-list {
            max-height: 660px;
            margin-top: 30px;

            @media (max-width:600px) {
                margin-top: calc(10px + (30 - 10) * ((100vw - 320px) / (600 - 320)));
            }


            li {
                width: 100%;
                align-items: center;
                gap: 10px;

                .message-box {
                    .message-details-box {
                        position: relative;
                        padding: 12px;
                        max-width: 245px;
                        display: inline-flex;
                        flex-direction: column;
                    }
                }

                +li {
                    margin-top: 20px;
                }
            }

            .left-chat-box {
                display: flex;
                justify-content: flex-start;

                .message-box {
                    display: flex;
                    align-items: flex-end;
                    gap: 12px;
                }

                .person-img {
                    @include pseudowh(35px, 35px);
                    object-fit: cover;
                    border-radius: 100%;
                }


                .message-details-box {
                    background-color: rgba(var(--content-color), 0.05);
                    border-radius: 20px 20px 20px 0;
                    border: 1px solid rgba(var(--content-color), 0.15);

                    h5 {
                        font-size: 14px;
                        font-weight: 400;
                        line-height: 1.5;
                        color: rgba(var(--title-color), 1);
                        letter-spacing: 0.5px;
                        word-wrap: break-word;
                    }

                    .timing {
                        display: flex;
                        align-items: center;
                        justify-content: flex-end;
                        gap: 5px;
                        margin-top: 8px;

                        h6 {
                            font-size: 12px;
                            font-weight: 500;
                            color: rgba(var(--content-color), 1);
                            letter-spacing: 0.5px;
                            word-wrap: break-word;
                        }
                    }

                    .voice-content {
                        display: flex;
                        align-items: center;
                        gap: 10px;

                        .play-button {
                            @include pseudowh(22px, 22px);
                            background: var(--theme-color);
                            border-radius: 100%;
                            position: relative;

                            &::before {
                                @include pos;
                                left: 8px;
                                top: 6px;
                                border-left: 9px solid rgba(var(--white), 1);
                                border-top: 5px solid transparent;
                                border-bottom: 5px solid transparent;
                            }
                        }

                        img {
                            width: 138px;
                            height: 26px;
                        }

                        h6 {
                            font-size: 12px;
                            font-weight: 400;
                            color: rgba(var(--content-color), 1);
                        }
                    }
                }
            }

            .right-chat-box {
                display: flex;
                justify-content: flex-end;
                margin-bottom: 0;

                .message-details-box {
                    background: var(--theme-color);
                    border-radius: 20px 20px 0 20px;

                    h5 {
                        color: rgba(var(--white), 1);
                        font-size: 14px;
                        font-weight: 400;
                        letter-spacing: .5px;
                        word-wrap: break-word;
                        display: inline-block;
                        margin-left: auto;
                    }

                    .timing {
                        display: flex;
                        align-items: center;
                        justify-content: flex-end;
                        gap: 5px;
                        margin-top: 8px;

                        h6 {
                            color: rgba(var(--white), 1);
                            font-weight: 500;
                            font-size: 12px;
                            line-height: 1.5;
                            letter-spacing: 0.5px;
                        }
                    }

                }

            }

            .forward {
                display: flex;
                align-items: center;
                gap: 14px;

                a {
                    background-color: rgba(var(--content-color), 0.1);
                    border-radius: 100%;
                    padding: 5px;
                }

                .media-img {
                    background-color: rgba(var(--content-color), 0.1);
                    border-radius: 20px 20px 0 20px;
                    display: flex;
                    justify-content: center;

                    img {
                        border-radius: 20px;
                        padding: 11px 9px;
                    }
                }
            }
        }
    }
}

.message-chat-time {
    font-size: 14px;
    font-weight: 500;
    color: rgba(var(--content-color), 1);
    text-align: center;
    margin-top: 36px;

    @media (max-width:600px) {
        margin-top: calc(25px + (36 - 25) * ((100vw - 320px) / (600 - 320)));
    }
}

/* =================
setting section CSS 
=====================*/
.chatting-theme-card {
    .theme-card {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 16px;
        border-radius: 16px;
        background-color: rgba(var(--light-blue), 0.12);
        border: 1px solid rgba(var(--light-blue), 1);

        .theme-info {
            display: flex;
            align-items: center;
            gap: 12px;

            .icon-container {
                @include flex_center;
                @include pseudowh(38px, 38px);
                border-radius: 50%;
                background-color: rgba(var(--white), 1);

                img {
                    @include pseudowh (20px, 20px);
                }
            }

            span {
                font-size: 14px;
                font-weight: 500;
                color: rgba(var(--title-color), 1);
            }
        }
    }

}

.chat-setting-list {
    margin-top: 30px;

    li {
        width: 100%;

        +li {
            margin-top: 25px;
            padding-top: 25px;
            border-top: 1px solid rgba(var(--content-color), 0.15);
        }

        a {
            display: flex;
            align-items: center;
            gap: 15px;

            .chat-icon {
                --Iconsax-Color: rgba(var(--title-color), 1);
                --Iconsax-Size: 24px;
            }

            .log-icon {
                rotate: 180deg;
                --Iconsax-Color: rgba(var(--title-color), 1);
                --Iconsax-Size: 22px;
            }

            .chat-setting-contant {
                width: calc(100% - 24px - 15px);
                display: flex;
                align-items: center;
                justify-content: space-between;

                h5 {
                    color: rgba(var(--title-color), 1);
                    font-size: 14px;
                    font-weight: 500;
                }

                .back-icon {
                    --Iconsax-Color: rgba(var(--title-color), 1);
                    --Iconsax-Size: 22px;
                }
            }

        }
    }
}


input[type="radio"] {
    display: none;

    &:checked+.message-round::after {
        @include pseudowh(10px, 10px);
        content: "";
        background: var(--theme-color);
        border-radius: 50%;
        transition: all 0.3s ease;
        opacity: 1;
        transform: scale(1);
    }
}

.fill-circle {
    display: flex;
    align-items: center;
    cursor: pointer;
    font-size: 14px;
    gap: 10px;
    font-weight: 500;
    color: rgba(var(--title-color), 1);
    padding-bottom: 10px;

    .message-round {
        @include pseudowh(18px, 18px);
        border: 1px solid rgba(var(--light-blue), 1);
        border-radius: 50%;
        display: inline-flex;
        align-items: center;
        justify-content: center;

        &::after {
            @include pos;
            opacity: 0;
            transform: scale(0);
            transition: all 0.3s ease;
            border-radius: 50%;
        }
    }
}

/* =================
language page CSS 
====================*/

.language-section {
    li {
        width: 100%;

        +li {
            margin-top: 18px;
        }

        input[type="radio"] {
            display: none;

            &:checked+label {
                border: 1px solid rgba(var(--light-blue), 1);
            }
        }

        .language-list-box {
            border: 1px solid rgba(var(--content-color), 0.2);
            border-radius: 18px;
            padding: 12px 10px;
            cursor: pointer;
            transition: all 0.3s ease-in-out;
            display: flex;
            align-items: center;
            gap: 10px;

            img {
                @include pseudowh(35px, 35px);
                object-fit: cover;
                border-radius: 100%;

                @media (max-width:600px) {
                    @include pseudowh(calc(30px + (35 - 30) * ((100vw - 320px) / (600 - 320))), calc(30px + (35 - 30) * ((100vw - 320px) / (600 - 320))))
                }
            }

            .language-contant {
                width: calc(100% - 10px - 35px);
                display: flex;
                align-items: center;
                justify-content: space-between;

                .language-name {
                    font-size: 16px;
                    font-weight: 600;
                    color: rgba(var(--title-color), 1);

                    @media (max-width:600px) {
                        font-size: calc(15px + (16 - 15) * ((100vw - 320px) / (600 - 320)));
                    }

                    span {
                        color: rgba(var(--title-color), 0.8);
                        font-size: 14px;
                        font-weight: 500;
                    }
                }

                .language-name2 {
                    font-size: 16px;
                    font-weight: 500;
                    color: rgba(var(--content-color), 1);

                    @media (max-width:600px) {
                        font-size: calc(15px + (16 - 15) * ((100vw - 320px) / (600 - 320)));
                    }
                }
            }
        }

    }
}

/* ==============
finger lock CSS 
==================*/
.finger-section {

    .lock-content {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 12px;

        @media (max-width:600px) {
            gap: calc(10px + (12 - 10) * ((100vw - 320px) / (600 - 320)));
        }

        .lock-icon {
            --Iconsax-Size: 35px;
            --Iconsax-Color: rgba(var(--light-blue), 1);

            @media (max-width:600px) {
                --Iconsax-Size: calc(28px + (35 - 28) * ((100vw - 320px) / (600 - 320)));
            }
        }

        h2 {
            text-align: center;
            font-size: 24px;
            font-weight: 700;
            color: rgba(var(--title-color), 1);

            @media (max-width:600px) {
                font-size: calc(20px + (24 - 20) * ((100vw - 320px) / (600 - 320)));
            }
        }
    }

    .finger-image {
        margin-right: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-block: 50px;

        @media (max-width:600px) {
            margin-block: calc(25px + (50 - 25) * ((100vw - 320px) / (600 - 320)));
        }

        img {
            width: 200px;
            height: 200px;

            @media (max-width:600px) {
                width: calc(150px + (200 - 150) * ((100vw - 320px) / (600 - 320)));
                height: calc(150px + (200 - 150) * ((100vw - 320px) / (600 - 320)));
            }
        }

    }

    div {
        text-align: center;

        h4 {
            font-size: 18px;
            font-weight: 600;
            color: rgba(var(--title-color), 1);
        }

        p {
            color: rgba(var(--content-color), 1);
            font-size: 14px;
            font-weight: 500;
            margin-top: 5px;
        }
    }
}

/* ===================
avatar page CSS 
======================*/
.avatar-box {
    background: var(--theme-color);
    position: relative;


    .select-avatar {
        display: flex;
        justify-content: center;

        img {
            @include pseudowh(230px, 230px);
            object-fit: cover;
            margin-top: 50px;
            border-radius: 100%;
            background-color: rgba(var(--white), 0.3);
            object-position: top;
            background: radial-gradient(circle, rgba(var(--white), 0.2) 0%, rgba(var(--white), 0.4) 100%);

            @media (max-width:600px) {
                @include pseudowh(calc(184px + (250 - 184) * ((100vw - 320px) / (600 - 320))), calc(184px + (250 - 184) * ((100vw - 320px) / (600 - 320))));
            }
        }

        img.square {
            border-radius: 8px;
        }
    }

    .avatar-list {
        background-color: rgba(var(--white), 1);
        border-radius: 30px 30px 0 0;
        max-width: 600px;
        margin: 35px auto 0;
        padding: 20px;
        overflow: auto;
        max-height: 400px;
        scrollbar-width: none;
        padding-bottom: 40px;

        ::-webkit-scrollbar {
            width: 0px;
            background: transparent;
        }

        @media (max-width:600px) {
            padding-inline: calc(10px + (20 - 10) * ((100vw - 320px) / (600 - 320)));
            padding-bottom: calc(20px + (40 - 20) * ((100vw - 320px) / (600 - 320)));
        }

        h3 {
            font-size: 20px;
            font-weight: 700;
            margin-bottom: 15px;
        }

        ul {
            display: grid;
            grid-template-columns: repeat(5, 1fr);
            gap: 15px;
            padding-bottom: 40px;

            @media (max-width:600px) {
                gap: calc(11px + (15 - 11) * ((100vw - 320px) / (600 - 320)));
            }

            @media (max-width:546.98px) {
                grid-template-columns: repeat(4, 1fr);
            }

            @media (max-width:435.98px) {
                grid-template-columns: repeat(3, 1fr);
            }

            li {
                @include flex_center;
                @include pseudowh(90px, 90px);
                padding: 5px;
                background-color: rgba(var(--light-blue), 0.07);
                border-radius: 50%;
                overflow: hidden;
                margin-inline: auto;
                border: 2px solid transparent;
                transition: all 0.2s ease-in-out;
                cursor: pointer;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    object-position: top;
                }

                .icon {
                    --Iconsax-Color: rgba(var(--title-color), 1);
                    --Iconsax-Size: 40px;
                }
            }

            .active {
                border: 2px solid rgba(var(--light-color), 1);
            }
        }

        .square-avatar-list {
            padding-bottom: 0;

            li {
                border-radius: 8px;
            }
        }

        .chatting-fixed-button {
            bottom: 0;
        }
    }
}


/* ==================
invited page CSS 
=====================*/
.invite-list-wrapper {
    li {
        width: 100%;

        +li {
            border-top: 1px solid rgba(var(--content-color), 0.15);
            padding-top: 16px;
            margin-top: 16px;
        }

        .invited-list-box {
            display: flex;
            align-items: center;
            gap: 10px;

            img {
                @include pseudowh(50px, 50px);
                border-radius: 5px;

                @media (max-width:600px) {
                    @include pseudowh(calc(45px + (50 - 45) * ((100vw - 320px) / (600 - 320))), calc(45px + (50 - 45) * ((100vw - 320px) / (600 - 320))))
                }
            }

            .invite-contant {
                width: calc(100% - 10px - 52px);
                display: flex;
                align-items: center;
                justify-content: space-between;

                h5 {
                    font-size: 16px;
                    font-weight: 600;
                    color: rgba(var(--title-color), 1);

                    @media (max-width:600px) {
                        font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (600 - 320)));
                    }
                }
            }

            h4 {
                font-size: 16px;
                font-weight: 500;
                color: rgba(var(--title-color), 1);
                background-color: rgba(var(--content-color), 0.13);
                border-radius: 50px;
                padding: 8px 18px;

                &:hover {
                    background: var(--theme-color);
                    color: rgba(var(--white), 1);
                }

                @media (max-width:600px) {
                    font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (600 - 320)));
                    padding-inline: calc(14px + (18 - 14) * ((100vw - 320px) / (600 - 320)));
                }

            }
        }
    }
}

/* ================
broadcast page  CSS  
===================*/
.chatting-list-title {
    font-size: 20px;
    font-weight: 700;
    color: rgba(var(--title-color), 1);
}

.chatting-list-wrapper {
    padding-top: 24px;
    height: 100%;

    @media (max-width:600) {
        margin-top: calc(35px + (55 - 35) * ((100vw - 320px) / (600 - 320)));
    }

    li {
        +li {
            border-top: 1px solid rgba(var(--content-color), 0.15);
            padding-top: 14px;
        }

        .chat-check-box {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        .chatting-contant {
            width: calc(100% - 10px - 50px);

            @media (max-width:600px) {
                width: calc(44px + (50 - 44) * ((100vw - 320px) / (600 - 320)));
                height: calc(44px + (50 - 44) * ((100vw - 320px) / (600 - 320)));
            }
        }

        a {
            display: flex;
            align-items: center;

            img {
                width: 50px;
                height: 50px;
                border-radius: 100%;

                @media (max-width:600px) {
                    width: calc(44px + (50 - 44) * ((100vw - 320px) / (600 - 320)));
                    height: calc(44px + (50 - 44) * ((100vw - 320px) / (600 - 320)));
                }
            }

            .chatting-name-box {
                h5 {
                    font-size: 16px;
                    font-weight: 600;
                    color: rgba(var(--title-color), 1);

                    @media (max-width:600px) {
                        font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (600 - 320)));
                    }
                }

                h6 {
                    @include text-ellipsis;
                    margin-top: 4px;
                    font-size: 14px;
                    font-weight: 600;
                    color: rgba(var(--content-color), 1);

                    @media (max-width:600px) {
                        font-size: calc(12px + (14 - 12) * ((100vw - 320px) / (600 - 320)));
                    }

                    span {
                        font-weight: 700;
                    }

                    .check-svg {
                        @include pseudowh(16px, 16px);
                        fill: var(--theme-color);
                    }

                    .chat-svg {
                        margin-top: -1px;
                        fill: rgba(var(--content-color), 1);
                    }

                    svg {
                        @include pseudowh(16px, 16px);
                        fill: var(--theme-color);
                        stroke: var(--theme-color);
                    }

                    .out-icon {
                        --Iconsax-Color: rgba(var(--online), 1);
                        --Iconsax-Size: 22px;
                        rotate: -44deg;
                        transform: scale(1.4);
                        margin-right: 6px;
                    }

                    .going-icon {
                        --Iconsax-Color: rgba(var(--error-color), 1);
                        --Iconsax-Size: 22px;
                        rotate: -44deg;
                        transform: scale(-1.4);
                        margin-right: 6px;
                    }

                }

                .fill-box {
                    background-color: rgba(var(--content-color), 0.09);
                    border-radius: 10px 10px 10px 0px;
                    padding: 10px;

                    .star-msg {
                        @include pseudowh(13px, 13px);
                        margin-top: -3px;
                        margin-left: 10px
                    }
                }

                .fill-text {
                    background: var(--theme-color);
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;
                }
            }
        }

        &.chatting-list-box {
            width: 100%;
            padding-bottom: 10px;
            display: flex;
            align-items: center;
            gap: 10px;

            .story {
                border-radius: 50%;
                background-color: rgba(var(--theme-default), 1);
                position: relative;
                padding: 5px;

                &::after {
                    @include pos;
                    inset: 0;
                    border-radius: 100%;
                    padding: 2px;
                    background: var(--theme-color);
                    -webkit-mask: linear-gradient(#ffff 0 0) content-box,
                        linear-gradient(#ffff 0 0);
                    mask-composite: exclude;
                    transition: all 0.4s ease-in-out;
                }
            }

            .story1 {
                border-radius: 50%;
                background-color: rgba(var(--theme-default), 1);
                position: relative;

                &::after {
                    @include pos;
                    width: calc(100% + 12px);
                    height: calc(100% + 12px);
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    border-radius: 100%;
                    padding: 2px;
                    background: #00ACFF;
                    -webkit-mask: linear-gradient(#ffff 0 0) content-box,
                        linear-gradient(#ffff 0 0);
                    mask-composite: exclude;
                    transition: all 0.4s ease-in-out;

                    @media (max-width: 600px) {
                        width: calc(100% + calc(8px + (12 - 8) * ((100vw - 320px) / (600 - 320))));
                        height: calc(100% + calc(8px + (12 - 8) * ((100vw - 320px) / (600 - 320))));
                    }
                }
            }

            &.online {
                .chatting-image {
                    position: relative;

                    &::after {
                        @include pos;
                        top: 0;
                        left: 2px;
                        width: 10px;
                        height: 10px;
                        background-color: #4DD68C;
                        border: 1.8px solid rgba(var(--white), 1);
                        border-radius: 100%;

                    }
                }

            }

            .chatting-contant {
                width: calc(100% - 10px - 50px);
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 15px;

                @media (max-width: 600px) {
                    gap: calc(7px + (15 - 7) * ((100vw - 320px) / (600 - 320)));
                    width: calc(100% - 10px - calc(44px + (50 - 44) * ((100vw - 320px) / (600 - 320))));
                }
            }

            .group-icon {
                --Iconsax-Color: rgba(var(--white), 1);
                --Iconsax-Size: 24px;
                background: var(--theme-color);
                border-radius: 100%;
                padding: 10px;
            }
        }

        .chatting-msg {
            .phone-icon {
                --Iconsax-Color: rgba(var(--title-color), 1);
                --Iconsax-Size: 24px;

                @media (max-width: 600px) {
                    --Iconsax-Size: calc(22px + (24 - 22) * ((100vw - 320px) / (600 - 320)));
                }
            }

            h6 {
                font-size: 13px;
                font-weight: 400;
                color: rgba(var(--content-color), 1);
                text-align: right;
                white-space: nowrap;

                &.fill-primary {
                    font-size: 13px;
                    font-weight: 400;
                    background: var(--theme-color);
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;
                }
            }

            .msg-count {
                @include flex_center;
                @include pseudowh(18px, 18px);
                border-radius: 100%;
                background: var(--theme-color);
                color: rgba(var(--white), 1);
                font-size: 11px;
                margin-left: auto;
                margin-top: 6px;
                line-height: 0
            }

            .pin {
                svg {
                    @include pseudowh(16px, 16px);
                    fill: rgba(var(--content-color), 1);
                    display: flex;
                    margin-left: auto;
                    margin-top: 6px;

                    @media (max-width: 600px) {
                        @include pseudowh(calc(14px + (16 - 14) * ((100vw - 320px) / (600 - 320))), calc(14px + (16 - 14) * ((100vw - 320px) / (600 - 320))));
                    }
                }
            }

            input[type=checkbox] {
                @include pseudowh(20px, 20px);
                position: relative;
                border: 2px solid rgba(var(--light-blue), 1);
                border-radius: 3px;
                background: none;
                cursor: pointer;
                outline: 0;
                padding: 0;
                vertical-align: text-top;
                appearance: none;
                opacity: 1;
                transition: all 0.3s ease-in-out;

                &:before {
                    @include pos;
                    right: 48%;
                    top: 50%;
                    width: 6px;
                    height: 12px;
                    border: solid rgba(var(--white), 1);
                    border-width: 0 2px 2px 0;
                    margin: -1px -1px 0 -1px;
                    transform: rotate(45deg) translate(-50%, -50%) scale(0, 0);
                    z-index: 2;
                    transition: all 0.3s ease-in-out;
                }


                &:checked {
                    background: var(--theme-color);

                    &::before {
                        transform: rotate(45deg) translate(-50%, -50%) scale(1, 1);
                    }
                }
            }
        }
    }
}

/* ============================
new-group CSS 
===============================*/
.new-group-content {
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    max-width: 600px;
    background-color: rgba(var(--white), 1);
    box-shadow: 0 2px 15px 0 rgba(var(--black), 0.08);
    z-index: 2;
    border: 1px solid rgba(var(--content-color), 0.2);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    padding-inline: 15px;
    padding-top: 0;

    .selected-person-list {
        display: flex;
        align-items: center;
        gap: 34px;
        scrollbar-width: none;
        overflow-x: auto;

        ::-webkit-scrollbar {
            width: 0px;
            background: transparent;
        }

        li {
            .selected-person-box {
                position: relative;
                width: 65px;
                text-align: center;
                padding-block: 16px 12px;

                .person-img {
                    @include pseudowh(60px, 60px);
                    object-fit: cover;
                    border-radius: 100%;
                }

                h5 {
                    @include text-ellipsis($line: 1);
                    font-size: 14px;
                    font-weight: 500;
                    color: rgba(var(--title-color), 1);
                    margin-top: 5px;
                    text-align: center;
                }

                .add {
                    color: rgba(var(--white), 1);
                    transform: rotate(45deg) translateY(-50%);
                    bottom: 24px;
                    right: 4px;
                    font-size: 16px;
                    font-weight: 500;
                    position: absolute;
                    background-color: rgba(var(--error-color), 1);
                    border-radius: 100%;
                    padding: 1px 4px;
                    border: 2px solid rgba(var(--white), 1);
                }

                .added {
                    background-color: rgba(var(--content-color), 1);
                }
            }
        }
    }
}


