/* ==============
story slider css
==================*/
.story-slider {
    .story-person-content {
        position: fixed;
        width: 100%;
        top: 0;
        padding: 30px 20px 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 12px;
        z-index: 2;
        max-width: 600px;

        @media (max-width:600px) {
            padding-top: calc(20px + (30 - 20) * ((100vw - 320px) / (600 - 320)));
        }

        .back-icon {
            --Iconsax-Color: rgba(var(--white), 1);
            --Iconsax-Size: 24px;
        }

        img {
            @include pseudowh(50px, 50px);
            border-radius: 100%;

            @media (max-width:600px) {
                @include pseudowh(calc(42px + (50 - 42) * ((100vw - 320px) / (600 - 320))), calc(42px + (50 - 42) * ((100vw - 320px) / (600 - 320))));
            }
        }

        h3 {
            color: rgba(var(--white), 1);
            font-size: 16px;
            font-weight: 600;
        }

        h4 {
            color: rgba(var(--white), 0.83);
            font-size: 14px;
            font-weight: 500;
        }
    }

    .story-like {
        @include pseudowh(30px, 30px);
        @include flex_center;
        position: fixed;
        bottom: 12px;
        right: 0;
        background: var(--theme-color);
        padding: 9px;
        max-width: 600px;
        left: 50%;
        transform: translate(-50%);
        z-index: 2;
        border-radius: 100%;

        .like-icon {
            --Iconsax-Color: rgba(var(--white), 1);
            --Iconsax-Size: 18px;
        }
    }

    .swiper-slide {
        img {
            width: 100%;
            height: 100vh;
            object-fit: cover;
        }
    }

    .swiper-pagination {
        position: absolute;
        bottom: unset !important;
        top: 14px;
        display: flex;
        // gap: 5px;
        flex-wrap: nowrap;
        padding-inline: 15px;

        @media (max-width:600px) {
            top: calc(10px + (14 - 10) * ((100vw - 320px) / (600 - 320)));
        }

        .swiper-pagination-bullet {
            position: relative;
            flex: 1;
            height: 5px;
            background-color: rgba(var(--white), 0.2);
            border-radius: 100px;
            overflow: hidden;

            @media (max-width:600px) {
                height: calc(3px + (5 - 3) * ((100vw - 320px) / (600 - 320)));
            }

            .progress-fill {
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: 0%;
                background: var(--theme-color);
                border-radius: 100px;
                transition: width 0.2s linear;
            }
        }

        .swiper-pagination-bullet-active {
            background-color: rgba(var(--white), 0.2);
        }
    }
}

