/*=====================
    tab CSS start
==========================*/
.ecommerce-tab {
    padding-inline: 15px;
    border-bottom: none;
    display: flex;
    flex-wrap: unset;
    width: 100%;
    overflow-x: scroll;
    margin-bottom: 20px;

    &::-webkit-scrollbar {
        width: 0;
        height: 0;
        background: transparent;
    }

    .nav-item {
        +.nav-item {
            margin-left: 10px;
        }
    }

    .nav-link {
        border: none;
        padding: 8px 12px;
        text-transform: capitalize;
        border-radius: 3px !important;
        margin-bottom: 0;
        white-space: nowrap;
        font-weight: 400;
        font-size: 14px;
        color: rgba(var(--title-color), 1);
        background-color: rgba(var(--light-bg), 1);

        &.active {
            font-weight: 500;
            color: rgba(var(--white), 1);
            background-color: rgba(var(--theme-color), 1);
        }
    }
}

.plant-tab {
    .nav-link {
        color: rgba(var(--content-color), 1);
        border-radius: 8px !important;

        &.active {
            font-weight: 600;
        }
    }
}

.grocery-auth-tab {
    margin-top: 78px;
    height: calc(100vh - 265px);

    .nav-tabs {
        display: flex;
        flex-wrap: nowrap;
        overflow: hidden;
        width: 100%;
        border-bottom: none;
        overflow-x: auto;

        .nav-item {
            color: rgba(var(--white), 0.1);
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            list-style-type: none;
            cursor: pointer;
            border-radius: 8px;
            transition: all 0.3s ease;
            border-radius: 0;
            border-bottom: 1px solid rgba(var(--border-color), 1);

            .nav-link {
                width: 100%;
                padding-top: 0;
                font-weight: 600;
                font-size: 16px;
                white-space: nowrap;
                color: rgba(var(--title-color), 1);
                background-color: transparent;
                border-left: none;
                border-right: none;
                border-top: none;
                border-bottom: 1px solid rgba(var(--border), 1);

                &.active {
                    font-weight: 700;
                    background-color: transparent;
                    color: rgba(var(--theme-color), 1);
                    border-left: none;
                    border-right: none;
                    border-top: none;
                    border-bottom: 2px solid rgba(var(--theme-color), 1);
                    padding-left: 15px;
                    padding-right: 15px;
                    border-radius: 0;
                    width: 100%;

                    &:hover {
                        color: rgba(var(--theme-color), 1);
                    }
                }

                &:hover {
                    border-left: none;
                    border-right: none;
                    border-top: none;
                    color: rgba(var(--title-color), 1);
                }

                &:focus {
                    border-color: transparent;
                }
            }
        }
    }

    .tab-content {
        height: 100%;
        background-color: rgba(var(--light-bg), 1);

        .google-buttons {

            a {
                @include flex_center;
                gap: 10px;
                padding: 15px;
                margin-top: 15px;
                color: rgba(var(--title-color), 1) !important;
                background-color: rgba(var(--white), 1) !important;

                @media (max-width:600px) {
                    padding: calc(10px + (15 - 10) * ((100vw - 320px) / (600 - 320)));
                }

                .icon {
                    width: 20px;
                    height: 20px;
                }
            }
        }
    }
}

.grocery-tab {
    display: flex;
    align-items: center;
    gap: 10px;
    flex-wrap: nowrap;
    padding-inline: 15px;
    overflow-x: auto;

    li {
        margin-bottom: 15px;

        .category-item {
            display: flex;
            align-items: center;
            flex-direction: column;
            border: none;
            background-color: transparent;
            padding: 0;

            &.active {
                .category-image {
                    background-color: rgba(var(--theme-color), 0.2);
                }

                .category-name {
                    color: rgba(var(--theme-color), 1) !important;
                }
            }

            .category-image {
                width: 70px;
                height: 70px;
                @include flex_center;
                background-color: rgba(var(--light-bg), 1);
                border-radius: 10px;
            }
        }

        .category-name {
            margin-top: 8px;
            text-align: center;
            font-size: 14px;
            line-height: 1.3;
            font-weight: 500;
            color: rgba(var(--title-color), 1) !important;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
        }
    }
}

/* ============================
chatting notification page CSS 
===============================*/

.chatting-tab {
    height: calc(100vh - 265px);

    .nav-tabs {
        display: flex;
        flex-wrap: nowrap;
        overflow: hidden;
        width: 100%;
        overflow-x: auto;
        border-bottom: 1px solid rgba(var(--content-color), 0.15);

        .nav-item {
            width: 100%;

            .nav-link {
                width: 100%;
                font-weight: 600;
                font-size: 16px;
                white-space: nowrap;
                color: rgba(var(--title-color), 1);
                border: none;
                background-color: transparent;

                &.active {
                    font-weight: 700;
                    background-color: rgba(var(--light-blue), 1);
                    color: rgba(var(--white), 1);
                }

                &:hover {
                    border: none;
                }

                &:focus {
                    border-color: transparent;
                }
            }
        }
    }

    .tab-content {
        height: 100%;
    }

    .toggle-notification-list {
        margin-block: 30px;

        @media (max-width:600px) {
            margin-block: calc(20px + (30 - 20) * ((100vw - 320px) / (600 - 320)));
        }

        li {
            display: flex;
            align-items: center;
            gap: 12px;

            +li {
                margin-top: 25px;

                @media (max-width:600px) {
                    margin-top: calc(15px + (25 - 15) * ((100vw - 320px) / (600 - 320)))
                }
            }

            .bell-icon {
                --Iconsax-Color: rgba(var(--content-color), 1);
                --Iconsax-Size: 24px;
                background-color: rgba(var(--content-color), 0.11);
                padding: 10px;
                border-radius: 100%;

                @media (max-width:600px) {
                    --Iconsax-Size: calc(22px + (24 - 22) * ((100vw - 320px) / (600 - 320)));
                    padding: calc(7px + (10 - 7) * ((100vw - 320px) / (600 - 320)));
                }
            }
        }

        h4 {
            @include text-ellipsis($line: 1);
            font-size: 16px;
            font-weight: 500;
            color: rgba(var(--title-color), 1);


            @media (max-width:600px) {
                font-size: calc(15px + (16 - 15) * ((100vw - 320px) / (600 - 320)))
            }
        }

        h5 {
            @include text-ellipsis($line: 2);
            color: rgba(var(--content-color), 1);
            font-size: 14px;
            font-weight: 500;
            margin-top: 5px;

            @media (max-width:600px) {
                font-size: calc(13px + (14 - 13) * ((100vw - 320px) / (600 - 320)))
            }
        }

        .notification-content {
            display: flex;
            justify-content: space-between;
            gap: 5px;
            align-items: center;
            width: calc(100% - 10px - 44px);

            @media (max-width:600px) {
                width: calc(100% - 10px - calc(36px + (44 - 36) * ((100vw - 320px) / (600 - 320))));
            }

            h6 {
                position: relative;
                font-size: 14px;
                font-weight: 500;
                color: rgba(var(--content-color), 1);
                margin-top: 10px;
                text-align: right;
                min-width: 65px;

                @media (max-width:600px) {
                    font-size: calc(13px + (14 - 13) * ((100vw - 320px) / (600 - 320)));
                    margin-top: calc(8px + (10 - 8) * ((100vw - 320px) / (600 - 320)));
                }

                &::after {
                    @include pseudowh(10px, 10px);
                    @include pos;
                    background: var(--theme-color);
                    border-radius: 100%;
                    top: -20px;
                    right: 6px;

                    @media (max-width:600px) {
                        width: calc(6px + (10 - 6) * ((100vw - 320px) / (600 - 320)));
                        height: calc(6px + (10 - 6) * ((100vw - 320px) / (600 - 320)));

                    }
                }
            }

        }

        .toggle-button {
            display: flex;
            position: relative;
            padding: 0;

            span {
                @include pseudowh(60px, 32px);
                display: flex;
                position: relative;
                background-color: rgba(var(--content-color), 0.09);
                transition: 0.5s;
                cursor: pointer;
                box-shadow: 0px 1px 4px 0px rgba(var(--content-color), 0.25);
                border-radius: 100px;

                @media (max-width:600px) {
                    @include pseudowh(calc(44px + (60 - 44) * ((100vw - 320px) / (600 - 320))), calc(20px + (32 - 20) * ((100vw - 320px) / (600 - 320))));
                }

                &::before {
                    @include pos;
                    @include pseudowh(20px, 20px);
                    top: 6px;
                    left: 5px;
                    border-radius: 100%;
                    background-color: rgba(var(--content-color), 0.5);
                    transition: 0.5s;

                    @media (max-width:600px) {
                        @include pseudowh(calc(14px + (20 - 14) * ((100vw - 320px) / (600 - 320))), calc(14px + (20 - 14) * ((100vw - 320px) / (600 - 320))));
                        top: calc(3px + (6 - 3) * ((100vw - 320px) / (600 - 320)));
                    }
                }
            }

            input[type="checkbox"] {
                opacity: 0;
                width: 0;
                height: 0;

                &:checked+span {
                    background-color: rgba(var(--light-blue), 0.3);

                    &::before {
                        left: 35px;
                        background: var(--theme-color);

                        @media (max-width:600px) {
                            left: calc(26px + (35 - 26) * ((100vw - 320px) / (600 - 320)));
                        }
                    }
                }
            }
        }
    }

    .offcanvas-button {
        width: 100%;
        border: none;
        color: rgba(var(--error-color), 1);
        font-size: 16px;
        font-weight: 500;
        cursor: pointer;
        background-color: rgba(var(--error-color), 0.1);
        border: 1px solid rgba(var(--error-color), 0.6);
        padding: 18px;
        border-radius: 10px;
        line-height: 1;
        margin-top: 50px;

        @media (max-width:600px) {
            padding: calc(17px + (18 - 17) * ((100vw - 320px) / (600 - 320)));
            border-radius: calc(8px + (10 - 8) * ((100vw - 320px) / (600 - 320)));
            font-size: calc(15px + (16 - 15) * ((100vw - 320px) / (600 - 320)));
            margin-top: calc(25px + (50 - 25) * ((100vw - 320px) / (600 - 320)));
        }
    }
}

.history-list {
    margin-top: 30px;

    @media (max-width:600px) {
        margin-top: calc(15px + (30 - 15) * ((100vw - 320px) / (600 - 320)))
    }

    h3 {
        font-size: 18px;
        font-weight: 600;
        color: rgba(var(--title-color), 1);
        text-align: left;

        @media (max-width:600px) {
            font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (600 - 320)));
        }
    }

    +h3 {
        margin-top: 40px;

        @media (max-width:600px) {
            margin-top: calc(20px + (40 - 20) * ((100vw - 320px) / (600 - 320)))
        }

    }

}

/* ===================
rating page CSS 
======================*/
.review-star {
    @include flex_center;
    gap: 10px;
    margin-bottom: 25px;

    img {
        width: 22px;
    }

    h3 {
        color: rgba(var(--title-color), 1);
        font-size: 24px;
        font-weight: 600;
        margin-top: 0;
    }
}

.star-rating {
    @include flex_center;
    gap: 6px;


    input[type="checkbox"] {
        display: none;
    }

    label {
        font-size: 32px;
        color: rgba(var(--content-color), 0.47);
        cursor: pointer;
        transition: color 0.2s;
        padding: 0;
    }

    #star1:checked~label[for="star1"],
    #star2:checked~label[for="star2"],
    #star3:checked~label[for="star3"],
    #star4:checked~label[for="star4"],
    #star5:checked~label[for="star5"],
    #star6:checked~label[for="star6"],
    #star7:checked~label[for="star7"],
    #star8:checked~label[for="star8"],
    #star9:checked~label[for="star9"],
    #star10:checked~label[for="star10"] {
        color: rgba(var(--rating-color), 1);
    }
}


.popular-review {
    margin-top: 18px;

    li {
        width: 100%;

        +li {
            margin-top: 20px;
            padding-top: 20px;
            border-top: 1px solid rgba(var(--content-color), 0.19);
        }

        .popular-rating {
            display: flex;
            align-items: center;
            gap: 20px;

            @media (max-width:600px) {
                gap: calc(12px + (20 - 12) * ((100vw - 320px) / (600 - 320)));
            }

            img {
                width: 14px;
            }

            h4 {
                @include text-ellipsis($line: 1);
                font-size: 17px;
                font-weight: 700;
                color: rgba(var(--title-color), 1);

                @media (max-width:600px) {
                    font-size: calc(15px + (17 - 15) * ((100vw - 320px) / (600 - 320)));
                }
            }

            h6 {
                position: relative;
                font-size: 14px;
                font-weight: 500;
                color: rgba(var(--content-color), 1);
                margin-left: auto;
            }
        }

        h3 {
            @include text-ellipsis($line: 2);
            text-align: left;
            margin-top: 8px;
            font-size: 14px;
            font-weight: 500;
            color: rgba(var(--title-color), 0.6);
            line-height: 1.5;
        }

        .thumb-track {
            display: flex;
            align-items: center;
            gap: 15px;
            margin-top: 18px;

            div {
                display: flex;
                gap: 6px;

                h5 {
                    color: rgba(var(--title-color), 0.6);
                    font-weight: 500;
                }
            }

        }
    }

    .tap-rate {
        .form-group {
            margin-top: 0;

            label {
                font-size: 16px;
                font-weight: 600;
                color: rgba(var(--title-color), 1);
            }

            input {
                margin-top: 8px;
                font-size: 14px;
                padding: 12px 8px;
                color: rgba(var(--content-color), 1);
                font-weight: 500;
                border: 1px solid rgba(var(--content-color), 0.23);

                &:focus {
                    outline: none;
                }
            }
        }

        h4 {
            color: rgba(var(--title-color), 1);
            font-size: 18px;
            font-weight: 600;
        }
    }
}

.load-more {
    text-align: center;
    margin: 40px auto 0;
    width: 100%;
    background: var(--theme-color);
    border-radius: 8px;

    .loadMore {
        color: rgba(var(--white), 1);
        border: none;
        opacity: 1;
    }
}

.product-load-more {
    .col-grid-box {
        display: none;
    }
}


/* ================
Hotel Review tab
===================*/

.hotel-review-tab {
    margin-top: 10px;
    background-color: rgba(var(--white), 1);
    border-radius: 100px;
    box-shadow: 0 2px 5px 0 rgba(var(--black), 0.03);

    .nav-tabs {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 12px;
        border: none;
        flex-wrap: nowrap;

        li {
            width: 100%;

            +li {
                border-left: 1px solid rgba(var(--content-color), 0.29);

            }

            .nav-link {
                width: 100%;
                font-weight: 500;
                font-size: 16px;
                white-space: nowrap;
                color: rgba(var(--title-color), 1);
                border: none;
                background-color: transparent;
                padding: 0;

                @media (max-width:600px) {
                    font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (600 - 320)));

                }

                &.active {
                    color: rgba(var(--theme-color), 1);
                }

                &:hover {
                    border: none;
                }

                &:focus {
                    border-color: transparent;
                }
            }
        }
    }
}

.transport-box {
    margin-top: 20px;

    @media (max-width:600px) {
        margin-top: calc(15px + (20 - 15) * ((100vw - 320px) / (600 - 320)));
    }

    .transport-title {
        display: flex;
        align-items: center;
        gap: 10px;

        @media (max-width:600px) {
            gap: calc(5px + (10 - 5) * ((100vw - 320px) / (600 - 320)));
        }

        .icon {
            --Iconsax-Color: rgba(var(--title-color), 1);
            --Iconsax-Size: 20px;
        }

        h5 {
            font-size: 16px;
            font-weight: 500;
            color: rgba(var(--title-color), 1);

            @media (max-width:600px) {
                font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (600 - 320)));

            }
        }
    }

    h3 {
        margin-top: 8px;
        font-size: 14px;
        font-weight: 500;
        color: rgba(var(--content-color), 1);

        @media (max-width:600px) {
            font-size: calc(13px + (14 - 13) * ((100vw - 320px) / (600 - 320)));
            margin-top: calc(6px + (8 - 6) * ((100vw - 320px) / (600 - 320)));
        }
    }

    h4 {
        font-size: 14px;
        font-weight: 500;
        color: rgba(var(--content-color), 1);

        @media (max-width:600px) {
            font-size: calc(13px + (14 - 13) * ((100vw - 320px) / (600 - 320)));
        }
    }

}

.attraction-list {
    padding-left: 22px;
    margin-top: 12px;

    li {
        display: list-item;
        list-style: disc;
        font-size: 14px;
        font-weight: 500;
        text-align: justify;
        color: rgba(var(--content-color), 1);
        line-height: 1.5;

        +li {
            margin-top: 12px;
        }
    }

}


.select-room-tab {
    margin-top: 24px;

    @media (max-width:600px) {
        margin-top: calc(15px + (24 - 15) * ((100vw - 320px) / (600 - 320)));

    }

    .nav-tabs {
        display: flex;
        flex-wrap: nowrap;
        gap: 12px;
        border: none;
        overflow-x: auto;
        scrollbar-width: none;

        &::-webkit-scrollbar {
            display: none;
        }


        .nav-item {
            border-radius: 8px;
            background-color: rgba(var(--light-gray), 1);

            .nav-link {
                width: 100%;
                font-weight: 400;
                font-size: 14px;
                white-space: nowrap;
                color: rgba(var(--content-color), 1);
                background-color: transparent;
                border: none;
                padding: 10px 12px;
                border-radius: 8px;

                &.active {
                    background-color: rgba(var(--theme-color), 1);
                    color: rgba(var(--white), 1);
                }

                &:hover {
                    border: none;
                }

                &:focus {
                    border-color: transparent;
                }
            }

        }
    }

    .tab-content {
        margin-top: 20px;

        .room-slide-wrapper {
            border: 1px solid rgba(var(--theme-color), 0.1);
            border-radius: 12px;
            margin-top: 10px;

            .room-slide {

                img {
                    width: 100%;
                    height: 182px;
                    object-fit: cover;
                    border-top-left-radius: 12px;
                    border-top-right-radius: 12px;
                }

                .swiper-pagination {
                    position: relative;
                    margin-top: 10px;
                    inset: unset;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .swiper-pagination-bullet {
                        @include pseudowh (8px, 8px);
                        background-color: rgba(var(--content-color), 0.46);
                        border-radius: 100px;
                        opacity: 1;
                    }

                    .swiper-pagination-bullet-active {
                        @include pseudowh (25px, 8px);
                        background-color: rgba(var(--theme-color), 1);
                    }
                }
            }

            .room-slide-content {
                background-color: rgba(var(--white), 1);
                border-bottom-left-radius: 12px;
                border-bottom-right-radius: 12px;
                padding: 15px 10px 12px;

                h4 {
                    font-size: 14px;
                    font-weight: 700;
                    color: rgba(var(--title-color), 1);
                }

                h5 {
                    margin-top: 3px;
                    color: rgba(var(--theme-color), 1);
                    font-size: 14px;
                    font-weight: 700;

                    span {
                        color: rgba(var(--content-color), 1);
                        font-weight: 500;
                        font-size: 13px;
                    }

                    h6 {
                        font-size: 13px;
                        font-weight: 700;
                        color: rgba(var(--title-color), 1);
                    }
                }

                .hotel-room-size {
                    display: flex;
                    gap: 28px;
                    margin-top: 14px;

                    li {
                        display: flex;
                        align-items: center;
                        gap: 5px;

                        h6 {
                            @include text-ellipsis($line:1);
                            font-size: 14px;
                            font-weight: 500;
                            color: rgba(var(--content-color), 1);
                        }
                    }
                }
            }
        }
    }
}

.hotel-category-panel {
    display: flex;
    padding: 12px;
    background-color: rgba(var(--white), 1);
    border: 1px solid rgba(var(--theme-color), 0.2);
    box-shadow: 0 2px 12px 0 rgba(var(--black), 0.03);
    border-radius: 10px;
    margin-top: 8px;

    li {
        width: 100%;

        +li {
            border-left: 1px solid rgba(var(--content-color), 0.29);
        }

        a {
            @include flex_center;
            gap: 6px;
            font-size: 16px;
            font-weight: 500;
            color: rgba(var(--title-color), 1);

            .icon {
                --Iconsax-Color: rgba(var(--title-color), 1);
                --Iconsax-Size: 14px;
            }
        }
    }
}


